import { useState, useEffect, useRef } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Settings, Upload, Trash2, Mail, Lock } from "lucide-react";
import { useUpdateProfile, useUploadAvatar } from "@/hooks/useProfile";
import { useAuth } from "@/contexts/AuthContext";
import { useUserRole } from "@/hooks/useUserRole";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "sonner";

const COUNTRY_CODES = [
  { code: "+351", country: "Portugal", flag: "" },
  { code: "+34", country: "Spain", flag: "" },
  { code: "+33", country: "France", flag: "" },
  { code: "+49", country: "Germany", flag: "" },
  { code: "+39", country: "Italy", flag: "" },
  { code: "+44", country: "UK", flag: "" },
  { code: "+31", country: "Netherlands", flag: "" },
  { code: "+32", country: "Belgium", flag: "" },
  { code: "+86", country: "China", flag: "" },
  { code: "+1", country: "USA/Canada", flag: "" },
  { code: "+55", country: "Brazil", flag: "" },
];

// Helper to parse phone with country code
const parsePhoneWithCode = (fullPhone: string | null | undefined) => {
  if (!fullPhone) return { code: "+351", number: "" };
  
  // Try to match country code at the start
  for (const country of COUNTRY_CODES) {
    if (fullPhone.startsWith(country.code)) {
      return { 
        code: country.code, 
        number: fullPhone.slice(country.code.length).trim() 
      };
    }
  }
  
  // If no match, return default code and full number
  return { code: "+351", number: fullPhone };
};

interface EditProfileDialogProps {
  profile: {
    display_name: string;
    phone?: string | null;
    avatar_url?: string | null;
  };
}

const EditProfileDialog = ({ profile }: EditProfileDialogProps) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { user } = useAuth();
  const { isAdmin } = useUserRole();
  const [open, setOpen] = useState(false);
  const [displayName, setDisplayName] = useState(profile.display_name);
  
  // Parse phone with country code
  const parsedPhone = parsePhoneWithCode(profile.phone);
  const [phoneCode, setPhoneCode] = useState(parsedPhone.code);
  const [phone, setPhone] = useState(parsedPhone.number);
  const [isEditingPhone, setIsEditingPhone] = useState(false);
  
  const [avatarFile, setAvatarFile] = useState<File | null>(null);
  const [avatarPreview, setAvatarPreview] = useState(profile.avatar_url || "");
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [deleteConfirmText, setDeleteConfirmText] = useState("");
  const [isDeleting, setIsDeleting] = useState(false);

  // Track previous open state to only sync when dialog first opens
  const prevOpenRef = useRef(false);
  
  // Sync profile data only when dialog transitions from closed to open
  useEffect(() => {
    const wasOpen = prevOpenRef.current;
    prevOpenRef.current = open;
    
    // Only sync data when dialog is opening (not when profile data changes while dialog is open)
    if (open && !wasOpen) {
      setDisplayName(profile.display_name);
      const newParsedPhone = parsePhoneWithCode(profile.phone);
      setPhoneCode(newParsedPhone.code);
      setPhone(newParsedPhone.number);
      setAvatarPreview(profile.avatar_url || "");
      setAvatarFile(null);
      setIsEditingPhone(false);
    }
  }, [open, profile.display_name, profile.phone, profile.avatar_url]);
  
  // Email change states
  const [showEmailDialog, setShowEmailDialog] = useState(false);
  const [showNewEmailDialog, setShowNewEmailDialog] = useState(false);
  const [showConfirmUpdateDialog, setShowConfirmUpdateDialog] = useState(false);
  const [newEmail, setNewEmail] = useState("");
  const [oldEmailCode, setOldEmailCode] = useState("");
  const [newEmailCode, setNewEmailCode] = useState("");
  const [oldEmailCodeSent, setOldEmailCodeSent] = useState(false);
  const [newEmailCodeSent, setNewEmailCodeSent] = useState(false);
  const [isUpdatingEmail, setIsUpdatingEmail] = useState(false);

  // Password change states
  const [showPasswordDialog, setShowPasswordDialog] = useState(false);
  const [showNewPasswordDialog, setShowNewPasswordDialog] = useState(false);
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [useEmailForPassword, setUseEmailForPassword] = useState(false);
  const [passwordEmailCode, setPasswordEmailCode] = useState("");
  const [passwordEmailCodeSent, setPasswordEmailCodeSent] = useState(false);
  const [isUpdatingPassword, setIsUpdatingPassword] = useState(false);
  const [passwordStrength, setPasswordStrength] = useState<'weak' | 'medium' | 'strong' | null>(null);

  const oldEmailCodeInputRef = useRef<HTMLInputElement>(null);
  const newEmailCodeInputRef = useRef<HTMLInputElement>(null);
  const passwordEmailCodeInputRef = useRef<HTMLInputElement>(null);

  const updateProfile = useUpdateProfile();
  const uploadAvatar = useUploadAvatar();

  // Auto-focus old email verification code input
  useEffect(() => {
    if (oldEmailCodeSent && oldEmailCodeInputRef.current) {
      oldEmailCodeInputRef.current.focus();
    }
  }, [oldEmailCodeSent]);

  // Auto-focus new email verification code input
  useEffect(() => {
    if (newEmailCodeSent && newEmailCodeInputRef.current) {
      newEmailCodeInputRef.current.focus();
    }
  }, [newEmailCodeSent]);

  // Auto-focus password email verification code input
  useEffect(() => {
    if (passwordEmailCodeSent && passwordEmailCodeInputRef.current) {
      passwordEmailCodeInputRef.current.focus();
    }
  }, [passwordEmailCodeSent]);

  const handleAvatarChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      if (file.size > 2 * 1024 * 1024) {
        return;
      }
      setAvatarFile(file);
      setAvatarPreview(URL.createObjectURL(file));
    }
  };

  const handleSave = async () => {
    let avatar_url = profile.avatar_url;

    if (avatarFile) {
      const uploadedUrl = await uploadAvatar.mutateAsync(avatarFile);
      if (uploadedUrl) avatar_url = uploadedUrl;
    }

    // Combine phone code and number
    const fullPhone = phone ? `${phoneCode} ${phone}` : undefined;

    await updateProfile.mutateAsync({
      display_name: displayName,
      phone: fullPhone,
      avatar_url: avatar_url || undefined,
    });

    setIsEditingPhone(false);
    setOpen(false);
  };

  const handleDeleteAccount = async () => {
    if (!user || deleteConfirmText.toLowerCase() !== "delete") return;
    
    setIsDeleting(true);
    try {
      // Call edge function to delete account
      const { error } = await supabase.functions.invoke("delete-account", {
        body: { userId: user.id },
      });
      
      if (error) {
        toast.error(t("profile.deleteAccountFailed"));
        console.error(error);
        setIsDeleting(false);
        return;
      }
      
      // Show success message first
      toast.success(t("profile.deleteAccountSuccess"));
      
      // Close dialogs
      setShowConfirmDialog(false);
      setShowDeleteDialog(false);
      setDeleteConfirmText("");
      
      // Wait a moment for user to see the message
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // Then sign out and navigate
      await supabase.auth.signOut();
      navigate("/auth");
    } catch (error: any) {
      toast.error(error.message || t("profile.deleteAccountFailed"));
      setIsDeleting(false);
    } finally {
      setIsDeleting(false);
    }
  };

  const handleSendOldEmailCode = async () => {
    if (!user?.email) return;
    
    try {
      const { error } = await supabase.functions.invoke("send-verification-code", {
        body: { email: user.email },
      });

      if (error) throw error;

      setOldEmailCodeSent(true);
      toast.success(t("profile.codeSentToOldEmail"));
    } catch (error: any) {
      toast.error(t("profile.sendCodeFailed"));
      console.error(error);
    }
  };

  const handleVerifyOldEmail = async () => {
    if (!oldEmailCode) {
      toast.error(t("profile.enterCodePlaceholder"));
      return;
    }
    
    try {
      const { data, error } = await supabase.functions.invoke("verify-email-code", {
        body: { email: user?.email, code: oldEmailCode },
      });

      if (error || !data?.success) {
        toast.error(t("profile.codeIncorrect"));
        return;
      }

      toast.success(t("profile.oldEmailVerified"));
      setShowEmailDialog(false);
      setShowNewEmailDialog(true);
    } catch (error: any) {
      toast.error(t("profile.verificationFailed"));
      console.error(error);
    }
  };

  const handleSendNewEmailCode = async () => {
    if (!newEmail || !newEmail.includes("@")) {
      toast.error(t("profile.enterValidEmail"));
      return;
    }
    
    try {
      const { error } = await supabase.functions.invoke("send-verification-code", {
        body: { email: newEmail },
      });

      if (error) throw error;

      toast.success(t("profile.codeSentToNewEmail"));
      setNewEmailCodeSent(true);
    } catch (error: any) {
      toast.error(t("profile.sendCodeFailed"));
      console.error(error);
    }
  };

  const handleVerifyNewEmail = async () => {
    if (!newEmailCode) {
      toast.error(t("profile.enterCodePlaceholder"));
      return;
    }
    
    try {
      const { data, error } = await supabase.functions.invoke("verify-email-code", {
        body: { email: newEmail, code: newEmailCode },
      });

      if (error || !data?.success) {
        toast.error(t("profile.codeIncorrect"));
        return;
      }

      toast.success(t("profile.newEmailVerified"));
      // Close current dialog and open confirm dialog
      setShowNewEmailDialog(false);
      // Use setTimeout to ensure smooth dialog transition
      setTimeout(() => {
        setShowConfirmUpdateDialog(true);
      }, 100);
    } catch (error: any) {
      toast.error(t("toast.verificationFailed"));
      console.error(error);
    }
  };

  const handleUpdateEmail = async () => {
    setIsUpdatingEmail(true);
    try {
      const { data: { session } } = await supabase.auth.getSession();
      if (!session || !user) {
        toast.error(t("toast.pleaseSignIn"));
        setIsUpdatingEmail(false);
        return;
      }

      // Update email
      const { data, error } = await supabase.functions.invoke("update-email", {
        body: {
          userId: user.id,
          oldEmail: user.email,
          oldEmailCode,
          newEmail,
          newEmailCode,
        },
        headers: {
          Authorization: `Bearer ${session.access_token}`,
        },
      });

      if (error || !data?.success) {
        throw new Error(data?.error || "Failed to update email");
      }

      toast.success(t("toast.emailUpdatedSuccess"));
      
      // Close dialogs
      setShowConfirmUpdateDialog(false);
      setOpen(false);
      
      // Reset email change states
      setOldEmailCode("");
      setNewEmail("");
      setNewEmailCode("");
      setOldEmailCodeSent(false);
      setNewEmailCodeSent(false);
      
      // Sign out and redirect to login
      await new Promise(resolve => setTimeout(resolve, 1500));
      await supabase.auth.signOut();
      navigate("/auth");
    } catch (error: any) {
      toast.error(error.message || t("toast.emailUpdateError"));
      console.error(error);
    } finally {
      setIsUpdatingEmail(false);
    }
  };

  // Password change handlers
  const checkPasswordStrength = (password: string): 'weak' | 'medium' | 'strong' => {
    let strength = 0;
    
    if (password.length >= 6) strength++;
    if (password.length >= 8) strength++;
    if (/[a-z]/.test(password)) strength++;
    if (/[A-Z]/.test(password)) strength++;
    if (/[0-9]/.test(password)) strength++;
    if (/[^a-zA-Z0-9]/.test(password)) strength++;
    
    if (strength <= 2) return 'weak';
    if (strength <= 4) return 'medium';
    return 'strong';
  };

  const handleVerifyPassword = async (e?: React.MouseEvent) => {
    e?.preventDefault();
    e?.stopPropagation();
    
    if (!currentPassword) {
      toast.error(t("profile.enterCurrentPassword"));
      return;
    }

    try {
      // Create a temporary client to verify password without triggering auth state changes
      const { createClient } = await import('@supabase/supabase-js');
      const tempClient = createClient(
        import.meta.env.VITE_SUPABASE_URL,
        import.meta.env.VITE_SUPABASE_PUBLISHABLE_KEY,
        {
          auth: {
            persistSession: false,
            autoRefreshToken: false,
          }
        }
      );

      const { error } = await tempClient.auth.signInWithPassword({
        email: user?.email || "",
        password: currentPassword,
      });

      if (error) {
        toast.error(t("profile.oldPasswordIncorrect"));
        return;
      }

      toast.success(t("profile.oldPasswordVerified"));
      setShowPasswordDialog(false);
      setShowNewPasswordDialog(true);
    } catch (error: any) {
      toast.error(t("profile.verificationFailed"));
      console.error(error);
    }
  };

  const handleSendPasswordEmailCode = async () => {
    if (!user?.email) return;
    
    try {
      const { error } = await supabase.functions.invoke("send-verification-code", {
        body: { email: user.email },
      });

      if (error) throw error;

      setPasswordEmailCodeSent(true);
      toast.success(t("profile.codeSentToOldEmail"));
    } catch (error: any) {
      toast.error(t("profile.sendCodeFailed"));
      console.error(error);
    }
  };

  const handleVerifyPasswordEmail = async (e?: React.MouseEvent) => {
    e?.preventDefault();
    e?.stopPropagation();
    
    if (!passwordEmailCode) {
      toast.error(t("profile.enterCodePlaceholder"));
      return;
    }
    
    try {
      const { data, error } = await supabase.functions.invoke("verify-email-code", {
        body: { email: user?.email, code: passwordEmailCode },
      });

      if (error || !data?.success) {
        toast.error(t("profile.codeIncorrect"));
        return;
      }

      toast.success(t("profile.oldEmailVerified"));
      setShowPasswordDialog(false);
      setShowNewPasswordDialog(true);
    } catch (error: any) {
      toast.error(t("profile.verificationFailed"));
      console.error(error);
    }
  };

  const handleUpdatePassword = async (e?: React.MouseEvent) => {
    e?.preventDefault();
    e?.stopPropagation();
    
    if (!newPassword || !confirmPassword) {
      toast.error(t("profile.enterNewPassword"));
      return;
    }

    if (newPassword.length < 6) {
      toast.error(t("profile.passwordTooShort"));
      return;
    }

    if (newPassword !== confirmPassword) {
      toast.error(t("profile.passwordsDontMatch"));
      return;
    }

    setIsUpdatingPassword(true);
    try {
      const { error } = await supabase.auth.updateUser({
        password: newPassword,
      });

      if (error) throw error;

      toast.success(t("profile.passwordUpdated"));
      
      // Close dialogs and reset states
      setShowNewPasswordDialog(false);
      setOpen(false);
      setCurrentPassword("");
      setNewPassword("");
      setConfirmPassword("");
      setUseEmailForPassword(false);
      setPasswordEmailCode("");
      setPasswordEmailCodeSent(false);
    } catch (error: any) {
      toast.error(error.message || t("profile.passwordUpdateFailed"));
      console.error(error);
    } finally {
      setIsUpdatingPassword(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button variant="outline">
          <Settings className="w-4 h-4 mr-2" />
          {t("profile.editProfile")}
        </Button>
      </DialogTrigger>
      <DialogContent className="max-w-[90vw] sm:max-w-[425px] max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>{t("profile.editProfile")}</DialogTitle>
          <DialogDescription>{t("profile.editProfileDescription")}</DialogDescription>
        </DialogHeader>
        <div className="space-y-3 py-3">
          <div className="flex flex-col items-center gap-3">
            <Avatar className="w-16 h-16 sm:w-24 sm:h-24">
              <AvatarImage src={avatarPreview} />
              <AvatarFallback>{displayName?.charAt(0).toUpperCase()}</AvatarFallback>
            </Avatar>
            <div>
              <Input
                type="file"
                accept="image/*"
                onChange={handleAvatarChange}
                className="hidden"
                id="avatar-upload"
              />
              <Button
                variant="outline"
                size="sm"
                onClick={() => document.getElementById("avatar-upload")?.click()}
                className="text-xs sm:text-sm"
              >
                <Upload className="w-3 h-3 sm:w-4 sm:h-4 mr-1 sm:mr-2" />
                {t("profile.uploadAvatar")}
              </Button>
            </div>
          </div>

          <div className="space-y-1.5">
            <Label htmlFor="displayName" className="text-sm">{t("profile.displayName")}</Label>
            <Input
              id="displayName"
              value={displayName}
              onChange={(e) => setDisplayName(e.target.value)}
              placeholder={t("profile.displayName")}
              className="h-9"
            />
          </div>

          <div className="space-y-1.5">
            <Label htmlFor="phone" className="text-sm">{t("profile.phone")}</Label>
            {!profile.phone && !isEditingPhone ? (
              <Button
                variant="outline"
                size="sm"
                onClick={() => setIsEditingPhone(true)}
                className="w-full justify-start h-9"
              >
                {t("profile.addPhoneNumber")}
              </Button>
            ) : isEditingPhone || !profile.phone ? (
              <div className="flex gap-2">
                <Select
                  value={phoneCode}
                  onValueChange={setPhoneCode}
                >
                  <SelectTrigger className="w-[110px] h-9">
                    <SelectValue placeholder="+351" />
                  </SelectTrigger>
                  <SelectContent>
                    {COUNTRY_CODES.map((item) => (
                      <SelectItem key={item.code} value={item.code}>
                        <span className="flex items-center gap-2">
                          <span>{item.flag}</span>
                          <span>{item.code}</span>
                        </span>
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                <Input
                  id="phone"
                  className="flex-1 h-9"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder={t("profile.phonePlaceholder")}
                />
              </div>
            ) : (
              <div className="flex items-center gap-2">
                <Input
                  value={`${phoneCode} ${phone}`}
                  disabled
                  className="h-9 flex-1"
                />
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => setIsEditingPhone(true)}
                  className="text-xs whitespace-nowrap"
                >
                  {t("profile.modifyPhoneNumber")}
                </Button>
              </div>
            )}
          </div>

          {/* Email change - only for non-admin users */}
          {!isAdmin && (
            <div className="space-y-1.5 pt-2 border-t">
              <Label className="text-sm">{t("profile.email")}</Label>
              <div className="flex items-center gap-2">
                <Input
                  value={user?.email || ""}
                  disabled
                  className="h-9 flex-1"
                />
                <Button
                  variant="outline"
                  size="sm"
                  onClick={() => setShowEmailDialog(true)}
                  className="text-xs"
                >
                  <Mail className="w-3 h-3 mr-1" />
                  {t("profile.modify")}
                </Button>
              </div>
            </div>
          )}

          {/* Password change - available for all users */}
          <div className={`space-y-1.5 ${isAdmin ? "pt-2 border-t" : ""}`}>
            <Label className="text-sm">{t("profile.changePassword")}</Label>
            <Button
              variant="outline"
              size="sm"
              onClick={() => setShowPasswordDialog(true)}
              className="w-full justify-start h-9"
            >
              <Lock className="w-3 h-3 mr-2" />
              {t("profile.changePassword")}
            </Button>
          </div>
        </div>
        <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-2 pt-2 border-t">
          {!isAdmin && (
            <Button 
              variant="destructive"
              size="sm"
              onClick={() => setShowDeleteDialog(true)}
              className="w-full sm:w-auto text-xs sm:text-sm"
            >
              <Trash2 className="w-3 h-3 sm:w-4 sm:h-4 mr-1 sm:mr-2" />
              {t("profile.deleteAccount")}
            </Button>
          )}
          <div className={`flex gap-2 w-full sm:w-auto ${isAdmin ? "sm:ml-auto" : ""}`}>
            <Button 
              variant="outline"
              size="sm"
              onClick={() => {
                setIsEditingPhone(false);
                setOpen(false);
              }}
              className="flex-1 sm:flex-none sm:min-w-[80px] text-xs sm:text-sm"
            >
              {t("common.cancel")}
            </Button>
            <Button
              size="sm"
              onClick={handleSave}
              disabled={updateProfile.isPending || uploadAvatar.isPending}
              className="flex-1 sm:flex-none sm:min-w-[80px] text-xs sm:text-sm"
            >
              {updateProfile.isPending || uploadAvatar.isPending
                ? t("common.loading")
                : t("common.save")}
            </Button>
          </div>
        </div>
      </DialogContent>

      {/* First confirmation dialog */}
      <AlertDialog open={showDeleteDialog} onOpenChange={setShowDeleteDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>{t("profile.deleteAccountWarning")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("profile.deleteAccountDescription")}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>{t("common.cancel")}</AlertDialogCancel>
            <AlertDialogAction
              onClick={() => {
                setShowDeleteDialog(false);
                setShowConfirmDialog(true);
              }}
              className="bg-destructive hover:bg-destructive/90"
            >
              {t("common.continue")}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Final confirmation dialog */}
      <AlertDialog open={showConfirmDialog} onOpenChange={setShowConfirmDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>{t("profile.confirmDeleteAccount")}</AlertDialogTitle>
            <AlertDialogDescription className="space-y-4">
              <p>{t("profile.confirmDeleteDescription")}</p>
              <div className="space-y-2">
                <Label htmlFor="confirm-delete">
                  {t("profile.typeDeleteToConfirm")}
                </Label>
                <Input
                  id="confirm-delete"
                  value={deleteConfirmText}
                  onChange={(e) => setDeleteConfirmText(e.target.value)}
                  placeholder="DELETE"
                  className="uppercase"
                />
              </div>
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => setDeleteConfirmText("")}>
              {t("common.cancel")}
            </AlertDialogCancel>
            <AlertDialogAction
              onClick={handleDeleteAccount}
              disabled={deleteConfirmText.toLowerCase() !== "delete" || isDeleting}
              className="bg-destructive hover:bg-destructive/90"
            >
              {isDeleting ? t("common.loading") : t("profile.deleteAccount")}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Verify old email dialog */}
      <AlertDialog open={showEmailDialog} onOpenChange={setShowEmailDialog}>
        <AlertDialogContent className="max-w-[90vw] sm:max-w-md">
          <AlertDialogHeader>
            <AlertDialogTitle>{t("profile.verifyOldEmail")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("profile.oldEmailStep")}
            </AlertDialogDescription>
          </AlertDialogHeader>
          
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label>{t("profile.currentEmail")}</Label>
              <div className="flex gap-2">
                <Input
                  value={user?.email || ""}
                  disabled
                  className="flex-1"
                />
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleSendOldEmailCode}
                  disabled={oldEmailCodeSent}
                >
                  {oldEmailCodeSent ? t("profile.codeSent") : t("profile.sendCode")}
                </Button>
              </div>
            </div>

            {oldEmailCodeSent && (
              <div className="space-y-2">
                <Label>{t("profile.verificationCode")}</Label>
                <Input
                  ref={oldEmailCodeInputRef}
                  value={oldEmailCode}
                  onChange={(e) => setOldEmailCode(e.target.value)}
                  placeholder={t("profile.enterCode")}
                  maxLength={4}
                />
              </div>
            )}
          </div>

          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => {
              setShowEmailDialog(false);
              // Reset states after cancel
              setTimeout(() => {
                setOldEmailCode("");
                setOldEmailCodeSent(false);
              }, 300);
            }}>{t("common.cancel")}</AlertDialogCancel>
            {oldEmailCodeSent && (
              <AlertDialogAction onClick={handleVerifyOldEmail}>
                {t("profile.verify")}
              </AlertDialogAction>
            )}
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* New email dialog - Input and verify in one step */}
      <AlertDialog open={showNewEmailDialog} onOpenChange={setShowNewEmailDialog}>
        <AlertDialogContent className="max-w-[90vw] sm:max-w-md">
          <AlertDialogHeader className="text-center space-y-3">
            <AlertDialogTitle className="text-xl">
              {t("profile.verifyNewEmail")}
            </AlertDialogTitle>
            <AlertDialogDescription className="text-center">
              {t("profile.newEmailStep")}
            </AlertDialogDescription>
          </AlertDialogHeader>
          
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label className="text-sm font-normal">{t("profile.newEmail")}</Label>
              <div className="flex gap-2">
                <Input
                  type="email"
                  value={newEmail}
                  onChange={(e) => setNewEmail(e.target.value)}
                  placeholder={t("profile.enterNewEmail")}
                  disabled={newEmailCodeSent}
                  className="border-2 focus:border-primary flex-1"
                />
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleSendNewEmailCode}
                  disabled={!newEmail || newEmailCodeSent}
                  className="text-xs whitespace-nowrap px-3"
                >
                  {newEmailCodeSent ? t("profile.codeSent") : t("profile.sendCode")}
                </Button>
              </div>
            </div>

            {newEmailCodeSent && (
              <div className="space-y-2 animate-in fade-in slide-in-from-top-2 duration-300">
                <Label className="text-sm font-normal">{t("profile.verificationCode")}</Label>
                <Input
                  ref={newEmailCodeInputRef}
                  value={newEmailCode}
                  onChange={(e) => setNewEmailCode(e.target.value)}
                  placeholder={t("profile.enterCode")}
                  maxLength={4}
                  className="border-2 focus:border-primary"
                />
              </div>
            )}
          </div>

          <AlertDialogFooter className="flex-col sm:flex-col gap-2">
            {newEmailCodeSent && (
              <AlertDialogAction 
                onClick={handleVerifyNewEmail}
                disabled={!newEmailCode || newEmailCode.length !== 4}
                className="w-full bg-primary hover:bg-primary/90"
              >
                
              </AlertDialogAction>
            )}
            <AlertDialogCancel 
              onClick={() => {
                setShowNewEmailDialog(false);
                // Reset states after a delay to allow dialog transition
                setTimeout(() => {
                  setNewEmail("");
                  setNewEmailCode("");
                  setNewEmailCodeSent(false);
                }, 300);
              }}
              className="w-full mt-0"
            >
              
            </AlertDialogCancel>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Confirm update dialog */}
      <AlertDialog open={showConfirmUpdateDialog} onOpenChange={setShowConfirmUpdateDialog}>
        <AlertDialogContent className="max-w-[90vw] sm:max-w-md">
          <AlertDialogHeader>
            <AlertDialogTitle></AlertDialogTitle>
            <AlertDialogDescription>
               3/3: 
            </AlertDialogDescription>
          </AlertDialogHeader>
          
          <div className="space-y-4 py-4">
            <div className="rounded-lg bg-green-50 dark:bg-green-950 p-4 text-sm">
              <p className="font-medium text-green-900 dark:text-green-100"></p>
              <p className="text-green-700 dark:text-green-300 mt-1">
                ""
              </p>
            </div>
            <div className="space-y-2">
              <div className="flex justify-between text-sm">
                <span className="text-muted-foreground">:</span>
                <span className="font-medium">{user?.email}</span>
              </div>
              <div className="flex justify-between text-sm">
                <span className="text-muted-foreground">:</span>
                <span className="font-medium">{newEmail}</span>
              </div>
            </div>
          </div>

          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => {
              setShowConfirmUpdateDialog(false);
              // Reset states after cancel
              setTimeout(() => {
                setOldEmailCode("");
                setNewEmail("");
                setNewEmailCode("");
                setOldEmailCodeSent(false);
                setNewEmailCodeSent(false);
              }, 300);
            }}></AlertDialogCancel>
            <AlertDialogAction 
              onClick={handleUpdateEmail}
              disabled={isUpdatingEmail}
            >
              {isUpdatingEmail ? "..." : ""}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Verify password/email dialog */}
      <AlertDialog open={showPasswordDialog} onOpenChange={setShowPasswordDialog}>
        <AlertDialogContent className="max-w-[90vw] sm:max-w-md">
          <AlertDialogHeader>
            <AlertDialogTitle>{t("profile.verifyOldPassword")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("profile.oldPasswordStep")}
            </AlertDialogDescription>
          </AlertDialogHeader>
          
          <div className="space-y-4 py-4">
            {!useEmailForPassword ? (
              <>
                <div className="space-y-2">
                  <Label>{t("profile.currentPassword")}</Label>
                  <Input
                    type="password"
                    value={currentPassword}
                    onChange={(e) => setCurrentPassword(e.target.value)}
                    placeholder={t("profile.enterCurrentPassword")}
                  />
                </div>
                <Button
                  variant="link"
                  size="sm"
                  onClick={() => setUseEmailForPassword(true)}
                  className="text-xs p-0 h-auto"
                >
                  {t("profile.forgotPassword")} {t("profile.useEmailVerification")}
                </Button>
              </>
            ) : (
              <>
                <div className="space-y-2">
                  <Label>{t("profile.currentEmail")}</Label>
                  <div className="flex gap-2">
                    <Input
                      value={user?.email || ""}
                      disabled
                      className="flex-1"
                    />
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={handleSendPasswordEmailCode}
                      disabled={passwordEmailCodeSent}
                    >
                      {passwordEmailCodeSent ? t("profile.codeSent") : t("profile.sendCode")}
                    </Button>
                  </div>
                </div>

                {passwordEmailCodeSent && (
                  <div className="space-y-2">
                    <Label>{t("profile.verificationCode")}</Label>
                    <Input
                      ref={passwordEmailCodeInputRef}
                      value={passwordEmailCode}
                      onChange={(e) => setPasswordEmailCode(e.target.value)}
                      placeholder={t("profile.enterCode")}
                      maxLength={4}
                    />
                  </div>
                )}
              </>
            )}
          </div>

          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => {
              setShowPasswordDialog(false);
              setTimeout(() => {
                setCurrentPassword("");
                setUseEmailForPassword(false);
                setPasswordEmailCode("");
                setPasswordEmailCodeSent(false);
              }, 300);
            }}>{t("common.cancel")}</AlertDialogCancel>
            {useEmailForPassword ? (
              passwordEmailCodeSent && (
                <AlertDialogAction onClick={handleVerifyPasswordEmail}>
                  {t("profile.verify")}
                </AlertDialogAction>
              )
            ) : (
              <AlertDialogAction onClick={handleVerifyPassword}>
                {t("profile.verify")}
              </AlertDialogAction>
            )}
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* New password dialog */}
      <AlertDialog open={showNewPasswordDialog} onOpenChange={setShowNewPasswordDialog}>
        <AlertDialogContent className="max-w-[90vw] sm:max-w-md">
          <AlertDialogHeader>
            <AlertDialogTitle>{t("profile.setNewPassword")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("profile.newPasswordStep")}
            </AlertDialogDescription>
          </AlertDialogHeader>
          
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label>{t("profile.newPassword")}</Label>
              <Input
                type="password"
                value={newPassword}
                onChange={(e) => {
                  setNewPassword(e.target.value);
                  if (e.target.value) {
                    setPasswordStrength(checkPasswordStrength(e.target.value));
                  } else {
                    setPasswordStrength(null);
                  }
                }}
                placeholder={t("profile.enterNewPassword")}
              />
              {passwordStrength && (
                <div className="space-y-1">
                  <div className="flex gap-1">
                    <div className={`h-1.5 flex-1 rounded-full transition-colors ${
                      passwordStrength === 'weak' ? 'bg-destructive' : 
                      passwordStrength === 'medium' ? 'bg-warning' : 
                      'bg-success'
                    }`} />
                    <div className={`h-1.5 flex-1 rounded-full transition-colors ${
                      passwordStrength === 'medium' || passwordStrength === 'strong' ? 
                      passwordStrength === 'medium' ? 'bg-warning' : 'bg-success' : 
                      'bg-muted'
                    }`} />
                    <div className={`h-1.5 flex-1 rounded-full transition-colors ${
                      passwordStrength === 'strong' ? 'bg-success' : 'bg-muted'
                    }`} />
                  </div>
                  <p className={`text-xs ${
                    passwordStrength === 'weak' ? 'text-destructive' : 
                    passwordStrength === 'medium' ? 'text-warning' : 
                    'text-success'
                  }`}>
                    {t(`auth.passwordStrength.${passwordStrength}`)}
                  </p>
                </div>
              )}
            </div>

            <div className="space-y-2">
              <Label>{t("profile.confirmNewPassword")}</Label>
              <Input
                type="password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                placeholder={t("profile.confirmNewPasswordPlaceholder")}
              />
            </div>

            {newPassword && confirmPassword && newPassword !== confirmPassword && (
              <p className="text-sm text-destructive">{t("profile.passwordsDontMatch")}</p>
            )}
          </div>

          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => {
              setShowNewPasswordDialog(false);
              setTimeout(() => {
                setNewPassword("");
                setConfirmPassword("");
                setPasswordStrength(null);
              }, 300);
            }}>{t("common.cancel")}</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleUpdatePassword}
              disabled={!newPassword || !confirmPassword || newPassword !== confirmPassword || isUpdatingPassword}
            >
              {isUpdatingPassword ? t("common.loading") : t("profile.updatePassword")}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </Dialog>
  );
};

export default EditProfileDialog;
