import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useUserAddresses, useCreateAddress, useUpdateAddress, useDeleteAddress, useSetDefaultAddress, type UserAddress, type AddressFormData } from "@/hooks/useUserAddresses";
import { useProfile, useUpdateProfile } from "@/hooks/useProfile";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from "@/components/ui/alert-dialog";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { MapPin, Plus, Edit, Trash2, Star, Home, Building, Briefcase, Phone } from "lucide-react";
const ADDRESS_LABELS = [{
  value: "Home",
  icon: Home,
  labelKey: "address.labelHome"
}, {
  value: "Work",
  icon: Briefcase,
  labelKey: "address.labelWork"
}, {
  value: "Office",
  icon: Building,
  labelKey: "address.labelOffice"
}, {
  value: "Other",
  icon: MapPin,
  labelKey: "address.labelOther"
}];
const COUNTRY_CODES = [{
  code: "+351",
  country: "Portugal",
  flag: ""
}, {
  code: "+34",
  country: "Spain",
  flag: ""
}, {
  code: "+33",
  country: "France",
  flag: ""
}, {
  code: "+49",
  country: "Germany",
  flag: ""
}, {
  code: "+39",
  country: "Italy",
  flag: ""
}, {
  code: "+44",
  country: "UK",
  flag: ""
}, {
  code: "+31",
  country: "Netherlands",
  flag: ""
}, {
  code: "+32",
  country: "Belgium",
  flag: ""
}, {
  code: "+86",
  country: "China",
  flag: ""
}, {
  code: "+1",
  country: "USA/Canada",
  flag: ""
}, {
  code: "+55",
  country: "Brazil",
  flag: ""
}];
const COUNTRIES = ["Portugal", "Spain", "France", "Germany", "Italy", "United Kingdom", "Netherlands", "Belgium", "China", "Other"];
interface AddressFormProps {
  initialData?: Partial<AddressFormData>;
  onSubmit: (data: AddressFormData) => void;
  onCancel: () => void;
  isLoading?: boolean;
  isEdit?: boolean;
}
const AddressForm = ({
  initialData,
  onSubmit,
  onCancel,
  isLoading,
  isEdit
}: AddressFormProps) => {
  const {
    t
  } = useTranslation();
  const [formData, setFormData] = useState<AddressFormData>({
    label: initialData?.label || "Home",
    street: initialData?.street || "",
    district: initialData?.district || "",
    city: initialData?.city || "",
    postal_code: initialData?.postal_code || "",
    country: initialData?.country || "Portugal",
    phone: initialData?.phone || "",
    phone_code: initialData?.phone_code || "+351",
    is_default: initialData?.is_default || false
  });
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };
  return <form onSubmit={handleSubmit} className="space-y-4">
      <div className="space-y-2">
        <Label htmlFor="label">{t("address.label")}</Label>
        <Select value={formData.label} onValueChange={value => setFormData({
        ...formData,
        label: value
      })}>
          <SelectTrigger>
            <SelectValue placeholder={t("address.selectLabel")} />
          </SelectTrigger>
          <SelectContent>
            {ADDRESS_LABELS.map(label => <SelectItem key={label.value} value={label.value}>
                <div className="flex items-center gap-2">
                  <label.icon className="w-4 h-4" />
                  {t(label.labelKey)}
                </div>
              </SelectItem>)}
          </SelectContent>
        </Select>
      </div>

      <div className="space-y-2">
        <Label htmlFor="street">{t("address.street")} *</Label>
        <Input id="street" value={formData.street} onChange={e => setFormData({
        ...formData,
        street: e.target.value
      })} placeholder={t("address.streetPlaceholder")} required />
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="district">{t("address.district")}</Label>
          <Input id="district" value={formData.district} onChange={e => setFormData({
          ...formData,
          district: e.target.value
        })} placeholder={t("address.districtPlaceholder")} />
        </div>
        <div className="space-y-2">
          <Label htmlFor="city">{t("address.city")} *</Label>
          <Input id="city" value={formData.city} onChange={e => setFormData({
          ...formData,
          city: e.target.value
        })} placeholder={t("address.cityPlaceholder")} required />
        </div>
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="postal_code">{t("address.postalCode")} *</Label>
          <Input id="postal_code" value={formData.postal_code} onChange={e => setFormData({
          ...formData,
          postal_code: e.target.value
        })} placeholder={t("address.postalCodePlaceholder")} required />
        </div>
        <div className="space-y-2">
          <Label htmlFor="country">{t("address.country")} *</Label>
          <Select value={formData.country} onValueChange={value => setFormData({
          ...formData,
          country: value
        })}>
            <SelectTrigger>
              <SelectValue placeholder={t("address.selectCountry")} />
            </SelectTrigger>
            <SelectContent>
              {COUNTRIES.map(country => <SelectItem key={country} value={country}>
                  {country}
                </SelectItem>)}
            </SelectContent>
        </Select>
        </div>
      </div>

      <div className="space-y-2">
        <Label htmlFor="phone">{t("address.phone")} *</Label>
        <div className="flex gap-2">
          <Select value={formData.phone_code} onValueChange={value => setFormData({
          ...formData,
          phone_code: value
        })}>
            <SelectTrigger className="w-[130px]">
              <SelectValue placeholder="+351" />
            </SelectTrigger>
            <SelectContent>
              {COUNTRY_CODES.map(item => <SelectItem key={item.code} value={item.code}>
                  <span className="flex items-center gap-2">
                    <span>{item.flag}</span>
                    <span>{item.code}</span>
                  </span>
                </SelectItem>)}
            </SelectContent>
          </Select>
          <Input id="phone" className="flex-1" value={formData.phone} onChange={e => setFormData({
          ...formData,
          phone: e.target.value
        })} placeholder={t("address.phonePlaceholder")} required />
        </div>
      </div>

      <div className="flex items-center space-x-2">
        <Checkbox id="is_default" checked={formData.is_default} onCheckedChange={checked => setFormData({
        ...formData,
        is_default: checked as boolean
      })} />
        <Label htmlFor="is_default" className="text-sm font-normal cursor-pointer">
          {t("address.setAsDefault")}
        </Label>
      </div>

      <DialogFooter className="gap-2 sm:gap-0">
        <Button type="button" variant="outline" onClick={onCancel}>
          {t("common.cancel")}
        </Button>
        <Button type="submit" disabled={isLoading}>
          {isLoading ? t("common.saving") : isEdit ? t("common.save") : t("address.add")}
        </Button>
      </DialogFooter>
    </form>;
};
interface AddressCardProps {
  address: UserAddress;
  onEdit: (address: UserAddress) => void;
  onDelete: (id: string) => void;
  onSetDefault: (id: string) => void;
  isDeleting?: boolean;
  isSettingDefault?: boolean;
  canDelete?: boolean;
}
const AddressCard = ({
  address,
  onEdit,
  onDelete,
  onSetDefault,
  isDeleting,
  isSettingDefault,
  canDelete = true
}: AddressCardProps) => {
  const {
    t
  } = useTranslation();
  const labelConfig = ADDRESS_LABELS.find(l => l.value === address.label) || ADDRESS_LABELS[3];
  const LabelIcon = labelConfig.icon;
  return <div className={`p-4 border rounded-lg hover:shadow-medium transition-shadow relative ${address.is_default ? 'bg-primary/5 border-primary/20' : ''}`}>
      <div className="flex items-start justify-between gap-4">
        <div className="flex items-start gap-3 flex-1">
          <div className={`p-2 rounded-full ${address.is_default ? 'bg-primary/20' : 'bg-primary/10'}`}>
            <LabelIcon className="w-5 h-5 text-primary" />
          </div>
          <div className="flex-1 min-w-0">
            <div className="flex items-center gap-2 mb-1">
              <span className="font-semibold text-foreground">
                {t(labelConfig.labelKey)}
              </span>
              {address.is_default && <Badge variant="secondary" className="text-xs">
                  <Star className="w-3 h-3 mr-1 fill-current" />
                  {t("address.default")}
                </Badge>}
            </div>
            <p className="text-sm text-foreground">
              {address.street}{address.district && `, ${address.district}`}
            </p>
            <p className="text-sm text-muted-foreground">
              {[address.postal_code, address.city, address.country].filter(Boolean).join(", ")}
            </p>
            {address.phone && <p className="text-sm text-muted-foreground flex items-center gap-1 mt-1">
                <Phone className="w-3 h-3" />
                {address.phone_code} {address.phone}
              </p>}
          </div>
        </div>

        <div className="flex items-center gap-1">
          {!address.is_default && <Button variant="ghost" size="icon" onClick={() => onSetDefault(address.id)} disabled={isSettingDefault} title={t("address.setAsDefault")}>
              <Star className="w-4 h-4" />
            </Button>}
          <Button variant="ghost" size="icon" onClick={() => onEdit(address)} title={t("common.edit")}>
            <Edit className="w-4 h-4" />
          </Button>
          {canDelete && <AlertDialog>
              <AlertDialogTrigger asChild>
                <Button variant="ghost" size="icon" className="text-destructive hover:text-destructive" title={t("common.delete")}>
                  <Trash2 className="w-4 h-4" />
                </Button>
              </AlertDialogTrigger>
              <AlertDialogContent>
                <AlertDialogHeader>
                  <AlertDialogTitle>{t("address.deleteTitle")}</AlertDialogTitle>
                  <AlertDialogDescription>
                    {t("address.deleteDescription")}
                  </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                  <AlertDialogCancel>{t("common.cancel")}</AlertDialogCancel>
                  <AlertDialogAction onClick={() => onDelete(address.id)} className="bg-destructive text-destructive-foreground hover:bg-destructive/90" disabled={isDeleting}>
                    {isDeleting ? t("common.deleting") : t("common.delete")}
                  </AlertDialogAction>
                </AlertDialogFooter>
              </AlertDialogContent>
            </AlertDialog>}
        </div>
      </div>
    </div>;
};
const AddressManagement = () => {
  const {
    t
  } = useTranslation();
  const {
    data: addresses,
    isLoading
  } = useUserAddresses();
  const {
    data: profile
  } = useProfile();
  const createAddress = useCreateAddress();
  const updateAddress = useUpdateAddress();
  const updateProfile = useUpdateProfile();
  const deleteAddress = useDeleteAddress();
  const setDefaultAddress = useSetDefaultAddress();
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false);
  const [editingAddress, setEditingAddress] = useState<UserAddress | null>(null);
  const [pendingAddressData, setPendingAddressData] = useState<AddressFormData | null>(null);
  const [showPhoneConfirm, setShowPhoneConfirm] = useState(false);
  const profileHasNoPhone = !profile?.phone;
  const handleCreate = (data: AddressFormData) => {
    // If profile has no phone and address has phone, show confirmation
    if (profileHasNoPhone && data.phone) {
      setPendingAddressData(data);
      setShowPhoneConfirm(true);
      return;
    }
    createAddress.mutate(data, {
      onSuccess: () => setIsAddDialogOpen(false)
    });
  };
  const handleConfirmCreateWithPhone = () => {
    if (!pendingAddressData) return;

    // Update profile phone
    const fullPhone = `${pendingAddressData.phone_code || '+351'} ${pendingAddressData.phone}`;
    updateProfile.mutate({
      phone: fullPhone
    });

    // Create address
    createAddress.mutate(pendingAddressData, {
      onSuccess: () => {
        setIsAddDialogOpen(false);
        setPendingAddressData(null);
        setShowPhoneConfirm(false);
      }
    });
  };
  const handleCancelPhoneConfirm = () => {
    setPendingAddressData(null);
    setShowPhoneConfirm(false);
  };
  const handleUpdate = (data: AddressFormData) => {
    if (!editingAddress) return;
    updateAddress.mutate({
      id: editingAddress.id,
      address: data
    }, {
      onSuccess: () => setEditingAddress(null)
    });
  };
  const handleDelete = (id: string) => {
    deleteAddress.mutate(id);
  };
  const handleSetDefault = (id: string) => {
    setDefaultAddress.mutate(id);
  };
  if (isLoading) {
    return <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <MapPin className="w-5 h-5" />
            {t("address.myAddresses")}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-center py-8 text-muted-foreground">
            {t("common.loading")}
          </div>
        </CardContent>
      </Card>;
  }
  const hasAddresses = addresses && addresses.length > 0;
  return <Card>
      <CardHeader>
        <div className="flex flex-col sm:flex-row sm:items-start sm:justify-between gap-3">
          <div className="space-y-1.5 min-w-0 flex-1">
            <CardTitle className="flex items-center gap-2">
              <MapPin className="w-5 h-5 flex-shrink-0" />
              {t("address.myAddresses")}
            </CardTitle>
            <CardDescription className="text-xs text-primary">{t("address.manageDescription")}</CardDescription>
          </div>
          {hasAddresses && <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
              <DialogTrigger asChild>
                <Button size="sm" className="w-full sm:w-auto flex-shrink-0">
                  <Plus className="w-4 h-4 mr-2" />
                  <span className="truncate">{t("address.addNew")}</span>
                </Button>
              </DialogTrigger>
              <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                  <DialogTitle>{t("address.addNew")}</DialogTitle>
                  <DialogDescription>
                    {t("address.addDescription")}
                  </DialogDescription>
                </DialogHeader>
                <AddressForm onSubmit={handleCreate} onCancel={() => setIsAddDialogOpen(false)} isLoading={createAddress.isPending} />
              </DialogContent>
            </Dialog>}
        </div>
      </CardHeader>
      <CardContent>
        {hasAddresses ? <div className="space-y-4">
            {addresses.map(address => <AddressCard key={address.id} address={address} onEdit={setEditingAddress} onDelete={handleDelete} onSetDefault={handleSetDefault} isDeleting={deleteAddress.isPending} isSettingDefault={setDefaultAddress.isPending} canDelete={addresses.length > 1} />)}
          </div> : <div className="text-center py-8">
            <MapPin className="w-12 h-12 mx-auto text-muted-foreground/50 mb-4" />
            <p className="text-muted-foreground mb-4">{t("address.noAddresses")}</p>
            <Button onClick={() => setIsAddDialogOpen(true)}>
              <Plus className="w-4 h-4 mr-2" />
              {t("address.addFirst")}
            </Button>
          </div>}
      </CardContent>

      {/* Edit Dialog */}
      <Dialog open={!!editingAddress} onOpenChange={() => setEditingAddress(null)}>
        <DialogContent className="sm:max-w-[500px]">
          <DialogHeader>
            <DialogTitle>{t("address.editAddress")}</DialogTitle>
            <DialogDescription>
              {t("address.editDescription")}
            </DialogDescription>
          </DialogHeader>
          {editingAddress && <AddressForm initialData={editingAddress} onSubmit={handleUpdate} onCancel={() => setEditingAddress(null)} isLoading={updateAddress.isPending} isEdit />}
        </DialogContent>
      </Dialog>

      {/* Phone Confirmation Dialog */}
      <AlertDialog open={showPhoneConfirm} onOpenChange={setShowPhoneConfirm}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>{t("address.phoneConfirmTitle")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("address.phoneConfirmDescription")}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={handleCancelPhoneConfirm}>
              {t("common.cancel")}
            </AlertDialogCancel>
            <AlertDialogAction onClick={handleConfirmCreateWithPhone} disabled={createAddress.isPending || updateProfile.isPending}>
              {createAddress.isPending || updateProfile.isPending ? t("common.saving") : t("common.confirm")}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </Card>;
};
export default AddressManagement;