import { useTranslation } from "react-i18next";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Heart } from "lucide-react";

interface ActivityFavoriteDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  activity: {
    title: string;
    image_url?: string | null;
    content: string;
  };
}

export function ActivityFavoriteDialog({
  open,
  onOpenChange,
  activity,
}: ActivityFavoriteDialogProps) {
  const { t } = useTranslation();

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-[90vw] sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="text-lg font-semibold line-clamp-2">
            {activity.title}
          </DialogTitle>
        </DialogHeader>
        
        <div className="space-y-4">
          {/* Favorited badge */}
          <Badge variant="secondary" className="flex items-center gap-1.5 w-fit">
            <Heart className="w-3.5 h-3.5 fill-primary text-primary" />
            {t("profile.activityFavorited")}
          </Badge>
          
          {/* Activity image */}
          {activity.image_url && (
            <div className="w-full aspect-video rounded-lg overflow-hidden bg-secondary/30">
              <img
                src={activity.image_url}
                alt={activity.title}
                className="w-full h-full object-cover"
              />
            </div>
          )}
          
          {/* Activity content - max 4 lines */}
          <div 
            className="text-muted-foreground text-sm prose prose-sm max-w-none line-clamp-4
              [&_span[style*='color']]:!text-inherit"
            dangerouslySetInnerHTML={{ __html: activity.content }}
          />
        </div>
      </DialogContent>
    </Dialog>
  );
}
