import { Link } from "react-router-dom";
import { ActivityFavoriteDialog } from "@/components/ActivityFavoriteDialog";
import { useTranslation } from "react-i18next";
import { memo, useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Carousel, CarouselContent, CarouselItem, CarouselApi } from "@/components/ui/carousel";
import { Heart, Star, Calendar, MessageSquare, Store, Users, MapPin, User, Ticket, Coins, History, ShoppingBag, AlertCircle, Clock } from "lucide-react";
import { useAuth } from "@/contexts/AuthContext";
import { useProfile } from "@/hooks/useProfile";
import { useFavorites } from "@/hooks/useFavorites";
import { useUserComments } from "@/hooks/useUserComments";
import { useSubscriptions } from "@/hooks/useActivities";
import { useUserRole } from "@/hooks/useUserRole";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { useMerchantActivities } from "@/hooks/useMerchantActivities";
import { useUserCoupons } from "@/hooks/useCoupons";
import EditProfileDialog from "@/components/EditProfileDialog";
import AddressManagement from "@/components/AddressManagement";
import ActivityTermsAgreement from "@/components/ActivityTermsAgreement";
import { CouponDetailDialog } from "@/components/CouponDetailDialog";
import { PointsHistoryDialog } from "@/components/PointsHistoryDialog";
import { BookingRecords } from "@/components/BookingRecords";
import { DeliveryRecords } from "@/components/DeliveryRecords";
import { formatDistanceToNow, format } from "date-fns";
import { useIsMobile } from "@/hooks/use-mobile";

// Memoized Comment Item Component
const CommentItem = memo(({ 
  comment, 
  getLocalizedField,
  t
}: { 
  comment: any; 
  getLocalizedField: (obj: any, field: string) => any;
  t: any;
}) => {
  const isFailed = comment.moderation_status === "flagged";
  const isPending = comment.moderation_status === "pending";
  
  // Parse risk reasons with localized labels
  const getRiskReasonText = () => {
    if (!comment.risk_reasons || !Array.isArray(comment.risk_reasons)) return null;
    
    const labels: Record<string, string> = {
      spam: t("riskReason.spam", ""),
      offensive: t("riskReason.offensive", ""),
      harassment: t("riskReason.harassment", ""),
      fake_review: t("riskReason.fakeReview", ""),
      flooding: t("riskReason.flooding", ""),
      irrelevant: t("riskReason.irrelevant", ""),
      sensitive_word_detected: t("riskReason.sensitiveWord", ""),
      parse_error: t("riskReason.parseError", ""),
    };
    
    return comment.risk_reasons.map((reason: string) => labels[reason] || reason).join(", ");
  };
  
  return (
    <div className="block p-3 md:p-4 rounded-lg border hover:shadow-medium transition-shadow">
      <Link
        to={`/${comment.target_type}/${comment.target_id}`}
        className="block"
      >
        <div className="flex items-start justify-between mb-2 gap-2">
          <h3 className="font-semibold text-card-foreground text-sm md:text-base line-clamp-1 flex-1">
            {getLocalizedField(comment, "target_name")}
          </h3>
          <span className="text-xs md:text-sm text-muted-foreground whitespace-nowrap">
            {formatDistanceToNow(new Date(comment.created_at), {
              addSuffix: true,
            })}
          </span>
        </div>
        <p className="text-muted-foreground line-clamp-2 text-sm md:text-base">{comment.content}</p>
        {comment.image_url && (
          <img
            src={comment.image_url}
            alt="Comment"
            className="mt-2 rounded-lg max-w-full md:max-w-xs"
            loading="lazy"
          />
        )}
      </Link>
      
      {/* Status badges */}
      {isPending && (
        <div className="mt-2 flex items-center gap-2">
          <Badge variant="outline" className="text-yellow-600 border-yellow-500 bg-yellow-50 dark:bg-yellow-950/30 text-xs">
            <Clock className="w-3 h-3 mr-1" />
            {t("restaurant.commentPending", "")}
          </Badge>
        </div>
      )}
      
      {isFailed && (
        <div className="mt-2 space-y-1.5">
          <Badge variant="outline" className="text-destructive border-destructive bg-destructive/10 text-xs">
            <AlertCircle className="w-3 h-3 mr-1" />
            {t("restaurant.commentFailed", "")}
          </Badge>
          {getRiskReasonText() && (
            <p className="text-xs text-destructive/80 pl-0.5">
              {t("restaurant.failReason", "")}: {getRiskReasonText()}
            </p>
          )}
        </div>
      )}
    </div>
  );
});
CommentItem.displayName = "CommentItem";

// Memoized Favorite Restaurant Item Component
const FavoriteRestaurantItem = memo(({ 
  restaurant, 
  getLocalizedField,
  t
}: { 
  restaurant: any; 
  getLocalizedField: (obj: any, field: string) => any;
  t: (key: string) => string;
}) => {
  // Skip rendering if restaurant is invalid
  if (!restaurant || !restaurant.id) {
    return null;
  }
  
  const restaurantName = getLocalizedField(restaurant, "name") || restaurant.name || "";
  const restaurantType = Array.isArray(restaurant.type) 
    ? restaurant.type.join("  ") 
    : (restaurant.type || "");
  
  return (
    <Link
      to={`/restaurant/${restaurant.id}`}
      state={{ from: "profile" }}
      className="flex gap-3 md:gap-4 p-3 md:p-4 rounded-lg border hover:shadow-medium transition-shadow"
    >
      <div className="w-20 h-20 md:w-24 md:h-24 rounded-lg overflow-hidden bg-secondary/30 flex-shrink-0">
        <img
          src={restaurant.image_url || "https://images.unsplash.com/photo-1555939594-58d7cb561ad1?w=400&q=80"}
          alt={String(restaurantName)}
          className="w-full h-full object-cover"
          loading="lazy"
          onError={(e) => {
            e.currentTarget.src = "https://images.unsplash.com/photo-1555939594-58d7cb561ad1?w=400&q=80";
          }}
        />
      </div>
      <div className="flex-1 min-w-0">
        <h3 className="font-semibold text-base md:text-lg text-card-foreground line-clamp-1">
          {restaurantName}
        </h3>
        <p className="text-muted-foreground text-sm mb-2 line-clamp-1">
          {restaurantType}
        </p>
        <div className="flex items-center gap-1">
          <Star className={`w-4 h-4 ${restaurant.rating ? "fill-accent text-accent" : "text-muted-foreground"}`} />
          {restaurant.rating ? (
            <span className="font-medium">{restaurant.rating.toFixed(1)}</span>
          ) : (
            <span className="text-xs text-muted-foreground text-center whitespace-nowrap">
              {t("restaurant.noRatingLine1")}<br />{t("restaurant.noRatingLine2")}
            </span>
          )}
        </div>
      </div>
    </Link>
  );
});
FavoriteRestaurantItem.displayName = "FavoriteRestaurantItem";

// Memoized Activity Subscription Item Component
const ActivitySubscriptionItem = memo(({ 
  subscription, 
  getLocalizedField,
  t,
  isMobile
}: { 
  subscription: any; 
  getLocalizedField: (obj: any, field: string) => any;
  t: (key: string) => string;
  isMobile?: boolean;
}) => {
  const [dialogOpen, setDialogOpen] = useState(false);
  
  return (
    <>
      <div
        onClick={() => setDialogOpen(true)}
        className={`block rounded-lg border hover:shadow-medium transition-shadow cursor-pointer ${isMobile ? "p-3" : "p-4"}`}
      >
        <div className={`flex items-start ${isMobile ? "gap-3" : "gap-4"}`}>
          {subscription.activity?.image_url && (
            <img
              src={subscription.activity.image_url}
              alt={String(getLocalizedField(subscription.activity, "title"))}
              className={`rounded-lg object-cover flex-shrink-0 ${isMobile ? "w-20 h-20" : "w-24 h-24"}`}
              loading="lazy"
            />
          )}
          <div className="flex-1 min-w-0">
            <h3 className={`font-semibold text-card-foreground mb-1 ${isMobile ? "text-sm" : ""}`}>
              {getLocalizedField(subscription.activity, "title")}
            </h3>
            <div 
              className={`text-muted-foreground line-clamp-2 prose max-w-none ${isMobile ? "text-xs prose-xs" : "text-sm prose-sm"}`}
              dangerouslySetInnerHTML={{ 
                __html: getLocalizedField(subscription.activity, "content") 
              }}
            />
            <p className={`text-muted-foreground mt-2 ${isMobile ? "text-xs" : "text-xs"}`}>
              {t("profile.joinedOn")}:{" "}
              {formatDistanceToNow(new Date(subscription.created_at), {
                addSuffix: true,
              })}
            </p>
          </div>
          <Heart className={`text-primary fill-primary flex-shrink-0 ${isMobile ? "w-4 h-4" : "w-5 h-5"}`} />
        </div>
      </div>
      
      <ActivityFavoriteDialog
        open={dialogOpen}
        onOpenChange={setDialogOpen}
        activity={{
          title: String(getLocalizedField(subscription.activity, "title")),
          image_url: subscription.activity?.image_url,
          content: String(getLocalizedField(subscription.activity, "content")),
        }}
      />
    </>
  );
});
ActivitySubscriptionItem.displayName = "ActivitySubscriptionItem";

// Memoized Merchant Activity Participation Item - grouped by activity
const MerchantActivityItem = memo(({ 
  activityGroup, 
  getLocalizedField,
  t,
  isMobile
}: { 
  activityGroup: {
    activity: any;
    restaurants: Array<{
      id: string;
      name: string;
      name_en: string | null;
      name_pt: string | null;
    }>;
    participationDate: string;
  };
  getLocalizedField: (obj: any, field: string) => any;
  t: (key: string) => string;
  isMobile?: boolean;
}) => {
  const activity = activityGroup.activity;
  const restaurants = activityGroup.restaurants;
  
  return (
    <div className={`rounded-lg border hover:shadow-medium transition-shadow ${isMobile ? "p-3" : "p-4"}`}>
      <Link
        to={`/activity/${activity.id}`}
        className={isMobile ? "block mb-2" : "block mb-3"}
      >
        <div className={`flex items-start ${isMobile ? "gap-3" : "gap-4"}`}>
          {activity.image_url && (
            <img
              src={activity.image_url}
              alt={String(getLocalizedField(activity, "title"))}
              className={`rounded-lg object-cover flex-shrink-0 ${isMobile ? "w-20 h-20" : "w-24 h-24"}`}
              loading="lazy"
            />
          )}
          <div className="flex-1 min-w-0">
            <h3 className={`font-semibold text-card-foreground mb-1 ${isMobile ? "text-sm" : ""}`}>
              {getLocalizedField(activity, "title")}
            </h3>
            <div 
              className={`text-muted-foreground line-clamp-2 prose max-w-none mb-2 ${isMobile ? "text-xs prose-xs" : "text-sm prose-sm"}`}
              dangerouslySetInnerHTML={{ 
                __html: getLocalizedField(activity, "content") 
              }}
            />
            <div className="flex items-center gap-2 md:gap-4 text-xs text-muted-foreground flex-wrap">
              <span className="flex items-center gap-1">
                <Calendar className="w-3 h-3" />
                {new Date(activity.start_date).toLocaleDateString()}
              </span>
              {activity.end_date && (
                <span>
                  - {new Date(activity.end_date).toLocaleDateString()}
                </span>
              )}
            </div>
          </div>
        </div>
      </Link>
      
      {/* Participating restaurants list */}
      <div className={`pt-2 md:pt-3 border-t ${isMobile ? "mt-2" : "mt-3"}`}>
        <div className={`flex items-center gap-2 ${isMobile ? "mb-1.5" : "mb-2"}`}>
          <Store className={`text-primary ${isMobile ? "w-3.5 h-3.5" : "w-4 h-4"}`} />
          <span className={`font-medium text-foreground ${isMobile ? "text-xs" : "text-sm"}`}>
            {t("profile.participatingRestaurants")} ({restaurants.length})
          </span>
        </div>
        <div className={`flex flex-wrap ${isMobile ? "gap-1.5" : "gap-2"}`}>
          {restaurants.map((restaurant) => (
            <Badge 
              key={restaurant.id}
              variant="secondary"
              className={isMobile ? "text-xs px-2 py-0.5" : "text-xs"}
            >
              {getLocalizedField(restaurant, "name")}
            </Badge>
          ))}
        </div>
      </div>
    </div>
  );
});
MerchantActivityItem.displayName = "MerchantActivityItem";

// Memoized User Coupon Item Component
const UserCouponItem = memo(({ 
  coupon, 
  getLocalizedField,
  t,
  isMobile
}: { 
  coupon: any; 
  getLocalizedField: (obj: any, field: string) => any;
  t: (key: string) => string;
  isMobile?: boolean;
}) => {
  const [detailsOpen, setDetailsOpen] = useState(false);
  const template = coupon.coupon_codes?.coupon_templates;
  const code = coupon.coupon_codes?.code;
  const isUsed = !!coupon.used_at;
  
  return (
    <>
      <div 
        className={`rounded-lg border transition-shadow cursor-pointer ${
          isUsed ? 'bg-muted/30' : 'hover:shadow-medium'
        } ${isMobile ? "p-3" : "p-4"}`}
        onClick={() => setDetailsOpen(true)}
      >
        <div className="flex items-start justify-between gap-3 mb-2">
          <div className="flex-1">
            <h3 className={`font-semibold text-card-foreground mb-1 ${isMobile ? "text-sm" : "text-base"}`}>
              {getLocalizedField(template, "title")}
            </h3>
            <p className={`text-muted-foreground line-clamp-2 ${isMobile ? "text-xs" : "text-sm"}`}>
              {getLocalizedField(template, "description")}
            </p>
          </div>
          <Badge variant={isUsed ? "secondary" : "default"} className={isMobile ? "text-xs" : ""}>
            {isUsed ? t("profile.coupons.used") : t("profile.coupons.available")}
          </Badge>
        </div>
        
        <div className={`flex items-center gap-2 mb-2 ${isMobile ? "text-xs" : "text-sm"}`}>
          <span className="font-mono bg-secondary/50 px-2 py-1 rounded">
            {code}
          </span>
          <span className="text-muted-foreground">
            {template?.discount_type === "percentage" 
              ? `${template?.discount_value}%` 
              : `${template?.discount_value}`}
          </span>
        </div>
        
        <div className={`text-muted-foreground ${isMobile ? "text-xs" : "text-sm"}`}>
          {isUsed ? (
            <span>{t("profile.coupons.usedOn")}: {format(new Date(coupon.used_at), "yyyy-MM-dd HH:mm")}</span>
          ) : template?.valid_until ? (
            <span>{t("profile.coupons.validUntil")}: {format(new Date(template.valid_until), "yyyy-MM-dd")}</span>
          ) : (
            <span>{t("profile.coupons.noExpiry")}</span>
          )}
        </div>
      </div>
      
      <CouponDetailDialog 
        open={detailsOpen}
        onOpenChange={setDetailsOpen}
        coupon={coupon}
      />
    </>
  );
});
UserCouponItem.displayName = "UserCouponItem";

const Profile = () => {
  const { t } = useTranslation();
  const { user } = useAuth();
  const isMobile = useIsMobile();
  const { data: profile, isLoading: profileLoading } = useProfile();
  const { data: favorites, isLoading: favoritesLoading } = useFavorites();
  const { data: comments, isLoading: commentsLoading } = useUserComments();
  const { data: subscriptions, isLoading: subscriptionsLoading } = useSubscriptions();
  const { data: merchantActivities, isLoading: merchantActivitiesLoading } = useMerchantActivities();
  const { data: userCoupons, isLoading: couponsLoading } = useUserCoupons();
  const { role, isMerchant, isSupervisor } = useUserRole();
  const { getLocalizedField } = useLocalizedContent();
  
  const isMerchantOrSupervisor = isMerchant || isSupervisor;
  
  // Mobile tab state
  const [activeTab, setActiveTab] = useState("profile");
  const [carouselApi, setCarouselApi] = useState<CarouselApi>();
  const [scrollPositions, setScrollPositions] = useState<Record<string, number>>({});
  const [visitedTabs, setVisitedTabs] = useState<Set<string>>(new Set(["profile"]));
  
  const tabs = isMerchantOrSupervisor ? [
    { value: "profile", label: t("profile.profile"), icon: User },
    { value: "favorites", label: t("profile.favorites"), icon: Heart },
    { value: "comments", label: t("profile.comments"), icon: MessageSquare },
    { value: "activities", label: t("profile.participatedActivities"), icon: Calendar },
  ] : [
    { value: "profile", label: t("profile.profile"), icon: User },
    { value: "favorites", label: t("profile.favorites"), icon: Heart },
    { value: "comments", label: t("profile.comments"), icon: MessageSquare },
  ];

  // Save current scroll position
  const saveScrollPosition = (tabValue: string) => {
    setScrollPositions(prev => ({
      ...prev,
      [tabValue]: window.scrollY
    }));
  };

  // Restore scroll position or scroll to top for new tabs
  const restoreScrollPosition = (tabValue: string) => {
    if (visitedTabs.has(tabValue)) {
      // Restore previous position
      const savedPosition = scrollPositions[tabValue] || 0;
      setTimeout(() => window.scrollTo(0, savedPosition), 0);
    } else {
      // New tab, scroll to top
      setTimeout(() => window.scrollTo(0, 0), 0);
      setVisitedTabs(prev => new Set([...prev, tabValue]));
    }
  };

  // Sync carousel with active tab
  useEffect(() => {
    if (!carouselApi) return;
    
    const handleSelect = () => {
      const index = carouselApi.selectedScrollSnap();
      const newTab = tabs[index].value;
      
      if (newTab !== activeTab) {
        // Save current tab's scroll position before switching
        saveScrollPosition(activeTab);
        // Restore new tab's scroll position
        restoreScrollPosition(newTab);
        setActiveTab(newTab);
      }
    };
    
    carouselApi.on("select", handleSelect);
    return () => {
      carouselApi.off("select", handleSelect);
    };
  }, [carouselApi, activeTab, scrollPositions, visitedTabs]);

  const handleTabClick = (tabValue: string) => {
    if (tabValue === activeTab) return;
    
    // Save current tab's scroll position
    saveScrollPosition(activeTab);
    
    const index = tabs.findIndex(t => t.value === tabValue);
    if (index !== -1 && carouselApi) {
      carouselApi.scrollTo(index);
    }
    
    // Restore new tab's scroll position
    restoreScrollPosition(tabValue);
    setActiveTab(tabValue);
  };

  if (!user || profileLoading) {
    return (
      <div className="min-h-screen bg-secondary/20 py-8 px-4">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center">{t("common.loading")}</div>
        </div>
      </div>
    );
  }

  const roleDisplay = role ? t(`profile.role.${role}`) : t("profile.role.user");

  return (
    <div className={`min-h-screen bg-secondary/20 ${isMobile ? "pb-20 pt-4" : "py-4 md:py-8 px-3 md:px-4"}`}>
      <div className={`container mx-auto max-w-6xl ${isMobile ? "px-3" : ""}`}>
        {/* Desktop/Tablet: Profile Header */}
        {!isMobile && (
          <>
            <Card className="mb-6 md:mb-8">
            <CardContent className="pt-4 md:pt-6 pb-4 md:pb-6">
              <div className="flex flex-col md:flex-row items-center md:items-start gap-4 md:gap-6">
                <Avatar className="w-24 h-24">
                  <AvatarImage src={profile?.avatar_url || ""} />
                  <AvatarFallback className="text-3xl">
                    {profile?.display_name?.charAt(0).toUpperCase() || "U"}
                  </AvatarFallback>
                </Avatar>
                <div className="flex-1 text-center md:text-left">
                  <h1 className="text-3xl font-bold mb-2 text-foreground">
                    {profile?.display_name}
                  </h1>
                  <p className="text-muted-foreground mb-2 md:mb-3">
                    {user.email}
                  </p>
                  {profile?.phone && (
                    <p className="text-muted-foreground mb-2 md:mb-3">
                      {profile.phone}
                    </p>
                  )}
                  {role && role !== 'user' && (
                    <Badge className="bg-primary capitalize">
                      {roleDisplay}
                    </Badge>
                  )}
                  {/* Points display */}
                  <div className="flex items-center gap-2 mt-2">
                    <Coins className="h-5 w-5 text-amber-500" />
                    <span className="font-semibold text-lg">{profile?.points || 0}</span>
                    <span className="text-muted-foreground">{t("profile.points", "")}</span>
                    <PointsHistoryDialog />
                  </div>
                </div>
                {/* Buttons container - horizontal layout */}
                <div className="flex flex-col sm:flex-row gap-2 items-start">
                  {profile && <EditProfileDialog profile={profile} />}
                  {/* Activity Terms Agreement for merchants/supervisors - Desktop */}
                  {isMerchantOrSupervisor && profile && (
                    <ActivityTermsAgreement 
                      isAgreed={profile?.activity_terms_agreed || false}
                      agreedAt={profile?.activity_terms_agreed_at || null}
                    />
                  )}
                </div>
              </div>
            </CardContent>
          </Card>
          </>
        )}

        {/* Tabs */}
        {isMobile ? (
          <>
            {/* Mobile: Swipeable content carousel */}
            <Carousel 
              setApi={setCarouselApi}
              opts={{
                align: "start",
                loop: false,
              }}
              className="w-full"
            >
              <CarouselContent className="-ml-0">
                {/* Profile Tab */}
                <CarouselItem className="pl-0">
                  <div className="pb-4 space-y-4">
                    <Card>
                      <CardContent className="pt-6 pb-6">
                        <div className="flex flex-col items-center gap-4">
                          <Avatar className="w-20 h-20">
                            <AvatarImage src={profile?.avatar_url || ""} />
                            <AvatarFallback className="text-2xl">
                              {profile?.display_name?.charAt(0).toUpperCase() || "U"}
                            </AvatarFallback>
                          </Avatar>
                          <div className="flex-1 text-center w-full">
                            <h1 className="text-2xl font-bold mb-2 text-foreground">
                              {profile?.display_name}
                            </h1>
                            <p className="text-muted-foreground text-sm mb-2">
                              {user.email}
                            </p>
                            {/* Mobile Points display */}
                            <div className="flex items-center justify-center gap-2 mb-2">
                              <Coins className="h-4 w-4 text-amber-500" />
                              <span className="font-semibold">{profile?.points || 0}</span>
                              <span className="text-muted-foreground text-sm">{t("profile.points", "")}</span>
                              <PointsHistoryDialog />
                            </div>
                            {profile?.phone && (
                              <p className="text-muted-foreground text-sm mb-3">
                                {profile.phone}
                              </p>
                            )}
                            {role && role !== 'user' && (
                              <Badge className="bg-primary capitalize text-xs mb-4">
                                {roleDisplay}
                              </Badge>
                            )}
                            {profile && (
                              <div className="mt-4 flex flex-col sm:flex-row gap-2 w-full">
                                <EditProfileDialog profile={profile} />
                                {/* Activity Terms Agreement for merchants/supervisors - Mobile */}
                                {isMerchantOrSupervisor && (
                                  <ActivityTermsAgreement 
                                    isAgreed={profile?.activity_terms_agreed || false}
                                    agreedAt={profile?.activity_terms_agreed_at || null}
                                  />
                                )}
                              </div>
                            )}
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                    
                    {/* My Coupons Section */}
                    <Card>
                      <CardHeader className="pb-3">
                        <CardTitle className="text-lg flex items-center gap-2">
                          <Ticket className="w-5 h-5" />
                          {t("profile.coupons.title")}
                        </CardTitle>
                        <CardDescription className="text-xs">{t("profile.coupons.description")}</CardDescription>
                      </CardHeader>
                      <CardContent className="px-3">
                        {couponsLoading ? (
                          <div className="text-center py-8 text-sm">{t("common.loading")}</div>
                        ) : userCoupons && userCoupons.length > 0 ? (
                          <div className="space-y-3">
                            {userCoupons.map((coupon) => (
                              <UserCouponItem
                                key={coupon.id}
                                coupon={coupon}
                                getLocalizedField={getLocalizedField}
                                t={t}
                                isMobile={true}
                              />
                            ))}
                          </div>
                        ) : (
                          <p className="text-muted-foreground text-center py-8 text-sm">
                            {t("profile.coupons.noCoupons")}{" "}
                            <Link to="/activities" className="text-primary hover:underline">
                              {t("profile.coupons.browseActivities")}
                            </Link>
                          </p>
                        )}
                      </CardContent>
                    </Card>
                    
                    {/* My Addresses Section - without title */}
                    <AddressManagement />
                    
                    {/* Booking Records Section */}
                    <BookingRecords />
                    
                    {/* Delivery Records Section */}
                    <DeliveryRecords />
                  </div>
                </CarouselItem>
                
                {/* Favorites Tab */}
                <CarouselItem className="pl-0">
                  <div className="pb-4">
                    <Card>
                      <CardContent className="pt-4">
                        <Tabs defaultValue="restaurants" className="w-full">
                          <TabsList className="grid w-full grid-cols-2 mb-4">
                            <TabsTrigger value="restaurants" className="flex items-center gap-2 text-xs">
                              <Store className="w-4 h-4" />
                              {t("profile.restaurantFavorites")}
                              {favorites && favorites.length > 0 && (
                                <Badge variant="secondary" className="ml-1 px-1.5 py-0 text-xs min-w-[20px] h-5">
                                  {favorites.length}
                                </Badge>
                              )}
                            </TabsTrigger>
                            <TabsTrigger value="activities" className="flex items-center gap-2 text-xs">
                              <Calendar className="w-4 h-4" />
                              {t("profile.activityFavorites")}
                              {subscriptions && subscriptions.length > 0 && (
                                <Badge variant="secondary" className="ml-1 px-1.5 py-0 text-xs min-w-[20px] h-5">
                                  {subscriptions.length}
                                </Badge>
                              )}
                            </TabsTrigger>
                          </TabsList>
                          
                          <TabsContent value="restaurants">
                            {favoritesLoading ? (
                              <div className="text-center py-8 text-sm">{t("common.loading")}</div>
                            ) : favorites && favorites.length > 0 ? (
                              <div className="grid gap-3 grid-cols-1">
                                {favorites.map((restaurant) => (
                                  <FavoriteRestaurantItem
                                    key={restaurant.id}
                                    restaurant={restaurant}
                                    getLocalizedField={getLocalizedField}
                                    t={t}
                                  />
                                ))}
                              </div>
                            ) : (
                              <p className="text-muted-foreground text-center py-8 text-sm">{t("profile.noFavorites")}</p>
                            )}
                          </TabsContent>
                          
                          <TabsContent value="activities">
                            {subscriptionsLoading ? (
                              <div className="text-center py-8 text-sm">{t("common.loading")}</div>
                            ) : subscriptions && subscriptions.length > 0 ? (
                              <div className="space-y-3">
                                {subscriptions.map((subscription) => (
                                  <ActivitySubscriptionItem
                                    key={subscription.activity_id}
                                    subscription={subscription}
                                    getLocalizedField={getLocalizedField}
                                    t={t}
                                    isMobile={true}
                                  />
                                ))}
                              </div>
                            ) : (
                              <p className="text-muted-foreground text-center py-8 text-sm">
                                {t("profile.noActivityFavorites")}{" "}
                                <Link to="/activities" className="text-primary hover:underline">
                                  {t("profile.browseActivities")}
                                </Link>
                              </p>
                            )}
                          </TabsContent>
                        </Tabs>
                      </CardContent>
                    </Card>
                  </div>
                </CarouselItem>
                
                {/* Comments Tab */}
                <CarouselItem className="pl-0">
                  <div className="pb-4">
                    <Card>
                      <CardHeader className="pb-3">
                        <CardTitle className="text-lg flex items-center gap-2">
                          <MessageSquare className="w-5 h-5" />
                          {t("profile.myComments")}
                        </CardTitle>
                        <CardDescription className="text-xs">{t("profile.commentsDesc")}</CardDescription>
                      </CardHeader>
                      <CardContent className="space-y-3 px-3">
                        {commentsLoading ? (
                          <div className="text-center py-8 text-sm">{t("common.loading")}</div>
                        ) : comments && comments.length > 0 ? (
                          comments.map((comment) => (
                            <CommentItem
                              key={comment.id}
                              comment={comment}
                              getLocalizedField={getLocalizedField}
                              t={t}
                            />
                          ))
                        ) : (
                          <p className="text-muted-foreground text-center py-8 text-sm">{t("profile.noComments")}</p>
                        )}
                      </CardContent>
                    </Card>
                  </div>
                </CarouselItem>
                
                {/* Activities Tab - Only for merchants/supervisors */}
                {isMerchantOrSupervisor && (
                  <CarouselItem className="pl-0">
                    <div className="pb-4">
                      <Card>
                        <CardHeader className="pb-3">
                          <CardTitle className="text-lg flex items-center gap-2">
                            <Calendar className="w-5 h-5" />
                            {t("profile.participatedActivities")}
                          </CardTitle>
                          <CardDescription className="text-xs">
                            {t("profile.participatedActivitiesDesc")}
                          </CardDescription>
                        </CardHeader>
                        <CardContent className="px-3">
                          <div className="space-y-4">
                            {merchantActivitiesLoading ? (
                              <div className="text-center py-8 text-sm">{t("common.loading")}</div>
                            ) : merchantActivities && merchantActivities.length > 0 ? (
                              <div>
                                <h3 className="font-semibold mb-3 flex items-center gap-2 text-base">
                                  <Store className="w-4 h-4" />
                                  {t("profile.restaurantActivities")}
                                </h3>
                                <div className="space-y-3">
                                  {(() => {
                                    const activityGroups = merchantActivities.reduce((acc, participation) => {
                                      const activityId = participation.activity_id;
                                      if (!acc[activityId]) {
                                        acc[activityId] = {
                                          activity: participation.activity,
                                          restaurants: [],
                                          participationDate: participation.created_at
                                        };
                                      }
                                      acc[activityId].restaurants.push({
                                        id: participation.restaurant_id,
                                        name: participation.restaurant_name,
                                        name_en: participation.restaurant_name_en,
                                        name_pt: participation.restaurant_name_pt,
                                      });
                                      return acc;
                                    }, {} as Record<string, any>);
                                    
                                    return Object.values(activityGroups).map((group: any) => (
                                      <MerchantActivityItem
                                        key={group.activity.id}
                                        activityGroup={group}
                                        getLocalizedField={getLocalizedField}
                                        t={t}
                                        isMobile={true}
                                      />
                                    ));
                                  })()}
                                </div>
                              </div>
                            ) : (
                              <p className="text-muted-foreground text-center py-8 text-sm">
                                {t("profile.noParticipatedActivities")}{" "}
                                <Link to="/activities" className="text-primary hover:underline">
                                  {t("profile.browseActivities")}
                                </Link>
                              </p>
                            )}
                          </div>
                        </CardContent>
                      </Card>
                    </div>
                  </CarouselItem>
                )}
              </CarouselContent>
            </Carousel>
            
            {/* Mobile: Fixed bottom navigation */}
            <div className="fixed bottom-0 left-0 right-0 bg-background border-t z-50">
              <div className="flex justify-around items-center py-2 px-2">
                {tabs.map((tab) => {
                  const Icon = tab.icon;
                  return (
                    <button
                      key={tab.value}
                      onClick={() => handleTabClick(tab.value)}
                      className={`flex flex-col items-center gap-1 py-2 px-3 rounded-lg transition-all ${
                        activeTab === tab.value
                          ? "text-primary"
                          : "text-muted-foreground"
                      }`}
                    >
                      <Icon className={`${activeTab === tab.value ? "w-6 h-6" : "w-5 h-5"}`} />
                      <span className="text-xs font-medium">{tab.label}</span>
                    </button>
                  );
                })}
              </div>
            </div>
          </>
        ) : (
          <Tabs defaultValue="favorites" className="space-y-4 md:space-y-6">
            <TabsList className={`grid w-full ${isMerchantOrSupervisor ? 'grid-cols-6' : 'grid-cols-5'}`}>
              <TabsTrigger value="favorites">
                <Heart className="w-4 h-4 mr-2" />
                {t("profile.favorites")}
              </TabsTrigger>
              <TabsTrigger value="coupons">
                <Ticket className="w-4 h-4 mr-2" />
                {t("profile.coupons.title")}
              </TabsTrigger>
              <TabsTrigger value="comments">
                <MessageSquare className="w-4 h-4 mr-2" />
                {t("profile.comments")}
              </TabsTrigger>
              {isMerchantOrSupervisor && (
                <TabsTrigger value="activities">
                  <Calendar className="w-4 h-4 mr-2" />
                  {t("profile.participatedActivities")}
                </TabsTrigger>
              )}
              <TabsTrigger value="addresses">
                <MapPin className="w-4 h-4 mr-2" />
                {t("address.myAddresses")}
              </TabsTrigger>
              <TabsTrigger value="orders">
                <Users className="w-4 h-4 mr-2" />
                {t("profile.orderRecords")}
              </TabsTrigger>
            </TabsList>

          <TabsContent value="favorites">
            <Card>
              <CardContent className="pt-6">
                <Tabs defaultValue="restaurants" className="w-full">
                  <TabsList className="grid w-full grid-cols-2 mb-6">
                    <TabsTrigger value="restaurants" className="flex items-center gap-2">
                      <Store className="w-4 h-4" />
                      {t("profile.restaurantFavorites")}
                      {favorites && favorites.length > 0 && (
                        <Badge variant="secondary" className="ml-1 px-1.5 py-0 text-xs min-w-[20px] h-5">
                          {favorites.length}
                        </Badge>
                      )}
                    </TabsTrigger>
                    <TabsTrigger value="activities" className="flex items-center gap-2">
                      <Calendar className="w-4 h-4" />
                      {t("profile.activityFavorites")}
                      {subscriptions && subscriptions.length > 0 && (
                        <Badge variant="secondary" className="ml-1 px-1.5 py-0 text-xs min-w-[20px] h-5">
                          {subscriptions.length}
                        </Badge>
                      )}
                    </TabsTrigger>
                  </TabsList>
                  
                  <TabsContent value="restaurants">
                    {favoritesLoading ? (
                      <div className="text-center py-8">{t("common.loading")}</div>
                    ) : favorites && favorites.length > 0 ? (
                      <div className="grid gap-4 md:grid-cols-2">
                        {favorites.map((restaurant) => (
                          <FavoriteRestaurantItem
                            key={restaurant.id}
                            restaurant={restaurant}
                            getLocalizedField={getLocalizedField}
                            t={t}
                          />
                        ))}
                      </div>
                    ) : (
                      <p className="text-muted-foreground text-center py-8">{t("profile.noFavorites")}</p>
                    )}
                  </TabsContent>
                  
                  <TabsContent value="activities">
                    {subscriptionsLoading ? (
                      <div className="text-center py-8">{t("common.loading")}</div>
                    ) : subscriptions && subscriptions.length > 0 ? (
                      <div className="space-y-4">
                        {subscriptions.map((subscription) => (
                          <ActivitySubscriptionItem
                            key={subscription.activity_id}
                            subscription={subscription}
                            getLocalizedField={getLocalizedField}
                            t={t}
                            isMobile={isMobile}
                          />
                        ))}
                      </div>
                    ) : (
                      <p className="text-muted-foreground text-center py-8">
                        {t("profile.noActivityFavorites")}{" "}
                        <Link to="/activities" className="text-primary hover:underline">
                          {t("profile.browseActivities")}
                        </Link>
                      </p>
                    )}
                  </TabsContent>
                </Tabs>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="comments">
            <Card>
              <CardHeader className={isMobile ? "pb-3" : ""}>
                <CardTitle className={`flex items-center gap-2 ${isMobile ? "text-lg" : ""}`}>
                  <MessageSquare className="w-5 h-5" />
                  {t("profile.myComments")}
                </CardTitle>
                <CardDescription className={isMobile ? "text-xs" : ""}>
                  {t("profile.commentsDesc")}
                </CardDescription>
              </CardHeader>
              <CardContent className={`space-y-3 md:space-y-4 ${isMobile ? "px-3" : ""}`}>
                {commentsLoading ? (
                  <div className={`text-center py-8 ${isMobile ? "text-sm" : ""}`}>
                    {t("common.loading")}
                  </div>
                ) : comments && comments.length > 0 ? (
                  comments.map((comment) => (
                    <CommentItem
                      key={comment.id}
                      comment={comment}
                      getLocalizedField={getLocalizedField}
                      t={t}
                    />
                  ))
                ) : (
                  <p className={`text-muted-foreground text-center py-8 ${isMobile ? "text-sm" : ""}`}>
                    {t("profile.noComments")}
                  </p>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          {isMerchantOrSupervisor && (
            <TabsContent value="activities">
              <Card>
                <CardHeader className={isMobile ? "pb-3" : ""}>
                  <CardTitle className={`flex items-center gap-2 ${isMobile ? "text-lg" : ""}`}>
                    <Calendar className="w-5 h-5" />
                    {t("profile.participatedActivities")}
                  </CardTitle>
                  <CardDescription className={isMobile ? "text-xs" : ""}>
                    {t("profile.participatedActivitiesDesc")}
                  </CardDescription>
                </CardHeader>
                <CardContent className={isMobile ? "px-3" : ""}>
                  <div className={isMobile ? "space-y-4" : "space-y-6"}>
                    {/* Restaurant participations */}
                    {merchantActivitiesLoading ? (
                      <div className={`text-center py-8 ${isMobile ? "text-sm" : ""}`}>
                        {t("common.loading")}
                      </div>
                    ) : merchantActivities && merchantActivities.length > 0 ? (
                      <div>
                        <h3 className={`font-semibold mb-3 md:mb-4 flex items-center gap-2 ${isMobile ? "text-base" : "text-lg"}`}>
                          <Store className={isMobile ? "w-4 h-4" : "w-5 h-5"} />
                          {t("profile.restaurantActivities")}
                        </h3>
                        <div className={isMobile ? "space-y-3" : "space-y-4"}>
                          {(() => {
                            // Group activities by activity_id
                            const activityGroups = merchantActivities.reduce((acc, participation) => {
                              const activityId = participation.activity_id;
                              if (!acc[activityId]) {
                                acc[activityId] = {
                                  activity: participation.activity,
                                  restaurants: [],
                                  participationDate: participation.created_at
                                };
                              }
                              acc[activityId].restaurants.push({
                                id: participation.restaurant_id,
                                name: participation.restaurant_name,
                                name_en: participation.restaurant_name_en,
                                name_pt: participation.restaurant_name_pt,
                              });
                              return acc;
                            }, {} as Record<string, any>);

                            return Object.values(activityGroups).map((group: any) => (
                              <MerchantActivityItem
                                key={group.activity.id}
                                activityGroup={group}
                                getLocalizedField={getLocalizedField}
                                t={t}
                                isMobile={isMobile}
                              />
                            ));
                          })()}
                        </div>
                      </div>
                    ) : (
                      <p className={`text-muted-foreground text-center py-8 ${isMobile ? "text-sm" : ""}`}>
                        {t("profile.noParticipatedActivities")}{" "}
                        <Link to="/activities" className="text-primary hover:underline">
                          {t("profile.browseActivities")}
                        </Link>
                      </p>
                    )}
                  </div>
                </CardContent>
              </Card>
            </TabsContent>
          )}

          <TabsContent value="coupons">
            <Card>
              <CardHeader className={isMobile ? "pb-3" : ""}>
                <CardTitle className={`flex items-center gap-2 ${isMobile ? "text-lg" : ""}`}>
                  <Ticket className="w-5 h-5" />
                  {t("profile.coupons.title")}
                </CardTitle>
                <CardDescription className={isMobile ? "text-xs" : ""}>
                  {t("profile.coupons.description")}
                </CardDescription>
              </CardHeader>
              <CardContent className={isMobile ? "px-3" : ""}>
                {couponsLoading ? (
                  <div className={`text-center py-8 ${isMobile ? "text-sm" : ""}`}>
                    {t("common.loading")}
                  </div>
                ) : userCoupons && userCoupons.length > 0 ? (
                  <div className={`grid gap-3 md:gap-4 ${isMobile ? "grid-cols-1" : "md:grid-cols-2"}`}>
                    {userCoupons.map((coupon) => (
                      <UserCouponItem
                        key={coupon.id}
                        coupon={coupon}
                        getLocalizedField={getLocalizedField}
                        t={t}
                        isMobile={isMobile}
                      />
                    ))}
                  </div>
                ) : (
                  <p className={`text-muted-foreground text-center py-8 ${isMobile ? "text-sm" : ""}`}>
                    {t("profile.coupons.noCoupons")}{" "}
                    <Link to="/activities" className="text-primary hover:underline">
                      {t("profile.coupons.browseActivities")}
                    </Link>
                  </p>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="addresses">
            <AddressManagement />
          </TabsContent>
          
          <TabsContent value="orders">
            <div className="space-y-4 md:space-y-6">
              <BookingRecords />
              <DeliveryRecords />
            </div>
          </TabsContent>
        </Tabs>
        )}
      </div>
    </div>
  );
};

export default Profile;
