import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";
import { useMerchantRestaurants } from "./useActivityParticipation";

interface MerchantActivityParticipation {
  activity_id: string;
  restaurant_id: string;
  restaurant_name: string;
  restaurant_name_en: string | null;
  restaurant_name_pt: string | null;
  restaurant_address: string;
  activity: {
    id: string;
    title_en: string;
    title_zh: string;
    title_pt: string;
    content_en: string;
    content_zh: string;
    content_pt: string;
    image_url: string | null;
    start_date: string;
    end_date: string | null;
    created_at: string;
  } | null;
  created_at: string;
}

export const useMerchantActivities = () => {
  const { user } = useAuth();
  const { data: myRestaurants = [] } = useMerchantRestaurants();

  return useQuery({
    queryKey: ["merchantActivities", user?.id],
    queryFn: async () => {
      if (!user || myRestaurants.length === 0) return [];

      const restaurantIds = myRestaurants.map(r => r.id);

      // Get all activity_restaurants entries for merchant's restaurants
      const { data: activityRestaurants, error } = await supabase
        .from("activity_restaurants")
        .select("*")
        .in("restaurant_id", restaurantIds)
        .order("created_at", { ascending: false });

      if (error) throw error;
      if (!activityRestaurants || activityRestaurants.length === 0) return [];

      // Fetch restaurant details
      const { data: restaurants } = await supabase
        .from("restaurants")
        .select("id, name, name_en, name_pt, address")
        .in("id", restaurantIds);

      // Fetch activity details - only published activities (draft activities should be invisible to merchants)
      const activityIds = [...new Set(activityRestaurants.map(ar => ar.activity_id))];
      const { data: activities } = await supabase
        .from("activities")
        .select("*")
        .in("id", activityIds)
        .eq("status", "published");

      // Combine data - filter out entries where activity is null (draft activities)
      const result: MerchantActivityParticipation[] = activityRestaurants
        .map(ar => {
          const restaurant = restaurants?.find(r => r.id === ar.restaurant_id);
          const activity = activities?.find(a => a.id === ar.activity_id);

          return {
            activity_id: ar.activity_id,
            restaurant_id: ar.restaurant_id,
            restaurant_name: restaurant?.name || "",
            restaurant_name_en: restaurant?.name_en || null,
            restaurant_name_pt: restaurant?.name_pt || null,
            restaurant_address: restaurant?.address || "",
            activity: activity || null,
            created_at: ar.created_at,
          };
        })
        .filter(item => item.activity !== null); // Remove draft activities

      return result;
    },
    enabled: !!user && myRestaurants.length > 0,
  });
};
