import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";

export interface BookingRecord {
  id: string;
  customer_email: string;
  customer_name: string | null;
  customer_phone: string | null;
  restaurant_id: string | null;
  restaurant_name: string;
  booking_date: string;
  booking_time: string;
  party_size: number;
  status: string;
  notes: string | null;
  created_at: string;
  updated_at: string;
  restaurants?: {
    name: string;
    name_en: string | null;
    name_pt: string | null;
    image_url: string | null;
  } | null;
}

export interface DeliveryRecord {
  id: string;
  customer_email: string;
  customer_name: string | null;
  customer_phone: string | null;
  restaurant_id: string | null;
  restaurant_name: string;
  order_date: string;
  total_amount: number | null;
  status: string;
  delivery_address: string | null;
  items: any[] | null;
  notes: string | null;
  created_at: string;
  updated_at: string;
  restaurants?: {
    name: string;
    name_en: string | null;
    name_pt: string | null;
    image_url: string | null;
  } | null;
}

// 
export const useBookingRecords = () => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["bookingRecords", user?.email],
    queryFn: async () => {
      if (!user?.email) return [];

      const { data, error } = await supabase
        .from("booking_records")
        .select(`
          *,
          restaurants (
            name,
            name_en,
            name_pt,
            image_url
          )
        `)
        .order("booking_date", { ascending: false });

      if (error) throw error;
      return data as BookingRecord[];
    },
    enabled: !!user?.email,
  });
};

// 
export const useDeliveryRecords = () => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["deliveryRecords", user?.email],
    queryFn: async () => {
      if (!user?.email) return [];

      const { data, error } = await supabase
        .from("delivery_records")
        .select(`
          *,
          restaurants (
            name,
            name_en,
            name_pt,
            image_url
          )
        `)
        .order("order_date", { ascending: false });

      if (error) throw error;
      return data as DeliveryRecord[];
    },
    enabled: !!user?.email,
  });
};
