import { useState } from "react";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import { Calendar, Tag, Ticket, Clock, QrCode } from "lucide-react";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { useTranslation } from "react-i18next";
import { format } from "date-fns";
import { Link } from "react-router-dom";
import { QRCodeSVG } from "qrcode.react";

interface CouponDetailDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  coupon: any;
}

export const CouponDetailDialog = ({ open, onOpenChange, coupon }: CouponDetailDialogProps) => {
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const [showQrCode, setShowQrCode] = useState(false);
  
  const template = coupon.coupon_codes?.coupon_templates;
  const code = coupon.coupon_codes?.code;
  const isUsed = !!coupon.used_at;

  return (
    <Dialog open={open} onOpenChange={(open) => {
      onOpenChange(open);
      if (!open) setShowQrCode(false);
    }}>
      <DialogContent className="max-w-lg max-h-[90vh]">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Ticket className="w-5 h-5 text-primary" />
            {t("profile.coupons.details")}
          </DialogTitle>
        </DialogHeader>

        <ScrollArea className="max-h-[70vh] pr-4">
          <div className="space-y-4">
            {/*  */}
            <div>
              <div className="flex items-start justify-between gap-3 mb-2">
                <h3 className="text-xl font-bold text-foreground">
                  {getLocalizedField(template, "title")}
                </h3>
                <Badge variant={isUsed ? "secondary" : "default"}>
                  {isUsed ? t("profile.coupons.used") : t("profile.coupons.available")}
                </Badge>
              </div>
              
              {template?.description_zh || template?.description_en || template?.description_pt ? (
                <p className="text-muted-foreground text-sm">
                  {getLocalizedField(template, "description")}
                </p>
              ) : null}
            </div>

            <Separator />

            {/*  */}
            <div 
              className="bg-secondary/30 p-4 rounded-lg cursor-pointer hover:bg-secondary/50 transition-colors"
              onClick={() => setShowQrCode(!showQrCode)}
            >
              <div className="flex flex-col items-center gap-3">
                {showQrCode ? (
                  <>
                    {/*  */}
                    <div className="bg-white p-3 rounded-lg">
                      <QRCodeSVG
                        value={code || ""}
                        size={140}
                        level="M"
                        includeMargin={false}
                      />
                    </div>
                    <p className="text-xs text-muted-foreground">
                      {t("profile.coupons.tapToHideQr")}
                    </p>
                  </>
                ) : (
                  <>
                    {/*  */}
                    <p className="text-2xl font-mono font-bold tracking-wider">
                      {code}
                    </p>
                    <div className="flex items-center gap-2 text-muted-foreground">
                      <QrCode className="w-4 h-4" />
                      <p className="text-xs">
                        {t("profile.coupons.tapToShowQr")}
                      </p>
                    </div>
                  </>
                )}
              </div>
            </div>

            {/*  */}
            <div className="flex items-center gap-2 p-3 bg-primary/10 rounded-lg">
              <Tag className="w-5 h-5 text-primary" />
              <div>
                <p className="text-sm font-medium text-foreground">
                  {t("profile.coupons.discount")}
                </p>
                <p className="text-xl font-bold text-primary">
                  {template?.discount_type === "percentage" 
                    ? `${template?.discount_value}% OFF` 
                    : `${template?.discount_value}`}
                </p>
              </div>
            </div>

            {/*  */}
            <div className="flex items-start gap-3">
              <Clock className="w-5 h-5 text-muted-foreground mt-0.5" />
              <div>
                <p className="text-sm font-medium text-foreground">
                  {t("profile.coupons.claimedAt")}
                </p>
                <p className="text-sm text-muted-foreground">
                  {format(new Date(coupon.claimed_at), "yyyy-MM-dd HH:mm")}
                </p>
              </div>
            </div>

            {/*  */}
            {isUsed && (
              <div className="flex items-start gap-3">
                <Calendar className="w-5 h-5 text-muted-foreground mt-0.5" />
                <div>
                  <p className="text-sm font-medium text-foreground">
                    {t("profile.coupons.usedOn")}
                  </p>
                  <p className="text-sm text-muted-foreground">
                    {format(new Date(coupon.used_at), "yyyy-MM-dd HH:mm")}
                  </p>
                </div>
              </div>
            )}

            {/*  */}
            <div className="flex items-start gap-3">
              <Calendar className="w-5 h-5 text-muted-foreground mt-0.5" />
              <div>
                <p className="text-sm font-medium text-foreground">
                  {t("profile.coupons.validity")}
                </p>
                <p className="text-sm text-muted-foreground">
                  {template?.valid_until 
                    ? `${format(new Date(template.valid_from), "yyyy-MM-dd")} - ${format(new Date(template.valid_until), "yyyy-MM-dd")}`
                    : t("profile.coupons.noExpiry")}
                </p>
              </div>
            </div>

            {/*  */}
            {template?.min_order_amount && template.min_order_amount > 0 && (
              <div className="flex items-start gap-3">
                <Tag className="w-5 h-5 text-muted-foreground mt-0.5" />
                <div>
                  <p className="text-sm font-medium text-foreground">
                    {t("profile.coupons.minOrder")}
                  </p>
                  <p className="text-sm text-muted-foreground">
                    {template.min_order_amount}
                  </p>
                </div>
              </div>
            )}

            {/*  */}
            {template?.max_discount_amount && template.max_discount_amount > 0 && (
              <div className="flex items-start gap-3">
                <Tag className="w-5 h-5 text-muted-foreground mt-0.5" />
                <div>
                  <p className="text-sm font-medium text-foreground">
                    {t("profile.coupons.maxDiscount")}
                  </p>
                  <p className="text-sm text-muted-foreground">
                    {template.max_discount_amount}
                  </p>
                </div>
              </div>
            )}

            <Separator />

            {/*  -  */}
            {coupon.activity_id && coupon.source === "share" && (
              <Link
                to={`/activity/${coupon.activity_id}`}
                className="flex items-center justify-center gap-2 p-3 rounded-lg border hover:shadow-md transition-all hover:border-primary"
              >
                <Tag className="w-4 h-4 text-primary" />
                <span className="text-sm font-medium text-foreground">
                  {t("profile.coupons.source")}: {t("profile.coupons.fromSharing")}
                </span>
                <span className="text-xs text-muted-foreground">
                  ({t("profile.coupons.viewActivity")})
                </span>
              </Link>
            )}
          </div>
        </ScrollArea>
      </DialogContent>
    </Dialog>
  );
};
