import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { format } from "date-fns";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { CalendarDays, Clock, Users, Store } from "lucide-react";
import { useBookingRecords } from "@/hooks/useOrderRecords";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";

export const BookingRecords = () => {
  const { t } = useTranslation();
  const { data: records, isLoading } = useBookingRecords();
  const { getLocalizedField } = useLocalizedContent();

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "confirmed":
        return <Badge className="bg-green-500">{t("booking.status.confirmed")}</Badge>;
      case "pending":
        return <Badge variant="secondary">{t("booking.status.pending")}</Badge>;
      case "cancelled":
        return <Badge variant="destructive">{t("booking.status.cancelled")}</Badge>;
      case "completed":
        return <Badge className="bg-blue-500">{t("booking.status.completed")}</Badge>;
      default:
        return <Badge variant="outline">{status}</Badge>;
    }
  };

  return (
    <Card>
      <CardHeader className="pb-3">
        <CardTitle className="text-lg flex items-center gap-2">
          <CalendarDays className="w-5 h-5" />
          {t("profile.bookingRecords.title")}
        </CardTitle>
        <CardDescription className="text-xs">
          {t("profile.bookingRecords.description")}
        </CardDescription>
      </CardHeader>
      <CardContent className="px-3">
        {isLoading ? (
          <div className="text-center py-8 text-sm">{t("common.loading")}</div>
        ) : records && records.length > 0 ? (
          <div className="space-y-3">
            {records.map((record) => {
              const restaurantName = record.restaurants 
                ? getLocalizedField(record.restaurants, "name") 
                : record.restaurant_name;
              
              return (
                <div
                  key={record.id}
                  className="p-3 rounded-lg border hover:shadow-medium transition-shadow"
                >
                  <div className="flex items-start justify-between gap-3 mb-2">
                    <div className="flex-1">
                      {record.restaurant_id ? (
                        <Link 
                          to={`/restaurant/${record.restaurant_id}`}
                          className="font-semibold text-sm hover:text-primary transition-colors"
                        >
                          <Store className="w-4 h-4 inline mr-1" />
                          {restaurantName}
                        </Link>
                      ) : (
                        <span className="font-semibold text-sm">
                          <Store className="w-4 h-4 inline mr-1" />
                          {restaurantName}
                        </span>
                      )}
                    </div>
                    {getStatusBadge(record.status)}
                  </div>
                  
                  <div className="flex flex-wrap gap-3 text-xs text-muted-foreground">
                    <span className="flex items-center gap-1">
                      <CalendarDays className="w-3 h-3" />
                      {format(new Date(record.booking_date), "yyyy-MM-dd")}
                    </span>
                    <span className="flex items-center gap-1">
                      <Clock className="w-3 h-3" />
                      {record.booking_time.slice(0, 5)}
                    </span>
                    <span className="flex items-center gap-1">
                      <Users className="w-3 h-3" />
                      {record.party_size} {t("booking.people")}
                    </span>
                  </div>
                  
                  {record.notes && (
                    <p className="text-xs text-muted-foreground mt-2 line-clamp-2">
                      {record.notes}
                    </p>
                  )}
                </div>
              );
            })}
          </div>
        ) : (
          <p className="text-muted-foreground text-center py-8 text-sm">
            {t("profile.bookingRecords.noRecords")}
          </p>
        )}
      </CardContent>
    </Card>
  );
};
