import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";

export const useUserComments = () => {
  const { user } = useAuth();

  return useQuery({
    queryKey: ["user-comments", user?.id],
    queryFn: async () => {
      if (!user) return [];

      // Fetch all user comments including flagged ones
      const { data: comments, error } = await supabase
        .from("comments")
        .select("*")
        .eq("user_id", user.id)
        .is("parent_id", null)
        .order("created_at", { ascending: false });

      if (error) throw error;
      if (!comments || comments.length === 0) return [];

      // Collect unique target IDs by type
      const restaurantIds = comments
        .filter(c => c.target_type === "restaurant")
        .map(c => c.target_id);
      const activityIds = comments
        .filter(c => c.target_type === "activity")
        .map(c => c.target_id);

      // Batch fetch restaurants
      const restaurantMap = new Map<string, { name: string; name_en?: string; name_pt?: string }>();
      if (restaurantIds.length > 0) {
        const { data: restaurants } = await supabase
          .from("restaurants")
          .select("id, name, name_en, name_pt")
          .in("id", restaurantIds);
        
        restaurants?.forEach(r => {
          restaurantMap.set(r.id, { name: r.name, name_en: r.name_en || undefined, name_pt: r.name_pt || undefined });
        });
      }

      // Batch fetch activities
      const activityMap = new Map<string, { title_zh: string; title_en?: string; title_pt?: string }>();
      if (activityIds.length > 0) {
        const { data: activities } = await supabase
          .from("activities")
          .select("id, title_zh, title_en, title_pt")
          .in("id", activityIds);
        
        activities?.forEach(a => {
          activityMap.set(a.id, { title_zh: a.title_zh, title_en: a.title_en, title_pt: a.title_pt });
        });
      }

      // Map comments with target info
      return comments.map(comment => {
        if (comment.target_type === "restaurant") {
          const restaurant = restaurantMap.get(comment.target_id);
          return {
            ...comment,
            target_name: restaurant?.name || "Unknown Restaurant",
            target_name_en: restaurant?.name_en,
            target_name_pt: restaurant?.name_pt,
          };
        } else if (comment.target_type === "activity") {
          const activity = activityMap.get(comment.target_id);
          return {
            ...comment,
            target_name: activity?.title_zh || "Unknown Activity",
            target_name_en: activity?.title_en,
            target_name_pt: activity?.title_pt,
          };
        }
        return comment;
      });
    },
    enabled: !!user,
    staleTime: 30000, // Cache for 30 seconds
  });
};
